<template>
	<el-container>
		<el-header>
			<!-- 头部导航 -->
			<nav>
				<div class="nav-menu">
					<h1>校 园 论 坛</h1>
					<!-- 导航菜单 -->
					<el-menu :default-active="$route.path" text-color="#FFF" active-text-color="#FFF" mode="horizontal" router>
						<el-menu-item index="/homePage">首页</el-menu-item>
						<el-menu-item index="/postListPage">帖子</el-menu-item>
						<el-menu-item index="/postingPage">发帖</el-menu-item>
						<el-menu-item index="/postManagePage">帖子管理</el-menu-item>
					</el-menu>
					
					<!-- 手机APP二维码 -->
					<el-popover placement="top-start" trigger="hover">
						<div>
							<img src="~@/assets/qr-img/app.png" width="100px"/>
							<p>扫码下载客户端</p>
						</div>
						<div slot="reference" class="color-white text-size-normal" style="padding: 20px;"><i class="el-icon-mobile-phone"></i>下载APP</div>
					</el-popover>
					
					<!-- 搜索框 -->
					<div class="search-input">
						<el-input ref="searchRef" placeholder="请输入搜索关键词" v-model="searchKeyword" suffix-icon="el-icon-search"></el-input>
						<ul class="search-list" v-if="showSearchList" v-infinite-scroll="loadSearchResult" @mouseleave="blurSearchInput">
							<li class="search-item" v-for="item in searchResultList" @click="routeToPostDetail(item.postId)">
								<span style="color: #FFA500; font-size: 12px;">【{{item.categoryName}}】</span>
								<span>{{item.title}}</span>
							</li>
							<div class="search-list-bottom">
								<span v-if="loading">加载中...</span>
								<span v-if="noMore">没有更多了</span>
							</div>
						</ul>
					</div>
				</div>
				<!-- 头像框 -->
				<avatar-component :userInfo="user" :messageNum="messageNum" style="padding-top: 10px;"></avatar-component>
			</nav>
		</el-header>
		<el-main>
			<router-view></router-view>
			<el-backtop :bottom="100">
				<div class="up">UP</div>
			</el-backtop>
			<div id="openApp">
			    <a >打开app</a>
			</div>
		</el-main>
	</el-container>
</template>

<script>
require("@/components/users/home/js/openApp")
import avatarComponent from '@/components/users/user_component/avatarComponent.vue'
import { getSearchList, getUser, getMessageNum } from '@/api/user'
import { responseStatus } from '@/utils/responseStatusUtil'
export default {
	name: 'home',
	inject: ['reload'],
	components:{
		avatarComponent: avatarComponent
	},
	data() {
		return {
			queryInfo: {
				pageNum: 1,
				pageSize: 5,
				total: 0
			},
			user: {},
			searchResultList: [],
			searchKeyword: ''	,//搜索关键字
			showSearchList: false,
			loading: false,
			messageNum: 0,
			messageTimer: null,
			token: null
		  }
	},
	computed:{
		noMore: function(){
			return this.queryInfo.isLastPage;
		}
	},
	methods:{
		
		
		/**
		 * 获取新消息
		 */
		handleGetMessageNum(){
			if(this.user.userId){
				getMessageNum().then(res =>{
					if(responseStatus(res))
						this.messageNum = res.data.data;
				})	
			}
		},
		
		handleMessageTimer(){
			if(this.user.userId){
				this.$timerCount[0] = setInterval(() => {
					this.handleGetMessageNum();
				},3000)
			}
		},
		
		/**
		 * 从网络获取用户信息
		 */
		handleGetUser(){
			if(this.token != null){
				getUser().then(res => {
					if(responseStatus(res)){
						this.user = res.data.data; 	
					}
				})
			}	
		},
		
		/**
		 * 搜索下拉框的显示与隐藏
		 */
		focusSearchInput(){
			this.showSearchList = true;
		},
		blurSearchInput(){
			this.$refs.searchRef.blur();
			this.showSearchList = false;	
		},
		
		/**
		 * 获得搜索结果列表
		 * @param {Object} searchKeyword 搜索关键字
		 */
		handleSearch(){
			const data = {
				content: this.searchKeyword,
				pageNum: this.queryInfo.pageNum,
				pageSize: this.queryInfo.pageSize,
			}
			if(this.searchKeyword != ''){
				getSearchList(data).then(res => {
					if(responseStatus(res)){
						this.searchResultList = this.searchResultList.concat(res.data.data.list);
						this.queryInfo = res.data.data;
						
					}
				})
			}
			
		},
		
		/**
		 * 搜索结果加载
		 */
		loadSearchResult() {
			if(this.queryInfo.hasNextPage){
				this.loading = true;
				this.queryInfo.pageNum += 1;
				this.handleSearch();
				this.loading = false;
			}
		},
		
		/*页面跳转*/
		routeToPostDetail(postId){
			this.$router.push({path: '/postDetailPage',query: {postId: postId}});
			this.reload();
		},
		
	
	},
	watch:{
		searchKeyword: function(){
			this.showSearchList = true;
			this.searchResultList = [];
			this.queryInfo.pageNum = 1;
			this.handleSearch();
		}
	},
	mounted() {
		this.token = window.localStorage.getItem('accessToken');
		this.handleGetUser();
		this.handleMessageTimer();
		this.$bus.$on('change', ()=> {
			this.reload();
		})
		
	}
}
</script>

<style>
	/* 二维码弹出框样式 */
	.el-popover {
		min-width: 100px !important;
		text-align: center !important;
		border-radius: 10px !important;
	}
</style>

<style lang="less" scoped>
	
	#openApp {
		width: 80%;
		color: #FFF;
		padding: 10px 20px;
		text-align: center;
		position: fixed;
		display: none;
		bottom: 10px;
		z-index: 10000;
		border-radius: 10px;
		background-color: #00C3FF;
	}
	
	/* 头部导航样式 */
	.el-header {
		height: max-content !important;
		background-color: rgba(0,0,0,0.1);	//背景颜色
		nav {
			display: flex;
			justify-content: space-between;
			.nav-menu {
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				//标题
				h1 {
					color: #FFF;
					font-size: 24px;
					margin-right: 20px;
				}
			}
			
			/* 搜索框样式 */
			.search-input {
				font-size: 14px;
				margin: 10px 20px;
				position: relative;
				.search-list {
					width: 100%;
					max-height: 250px;
					overflow: auto;
					background-color: #FFF;
					box-shadow: 0 15px 30px 0 rgba(0, 0, 1, .1);
					position: absolute;
					z-index: 100000;
					border-radius: 10px;
					.search-item {
						padding: 10px 20px;
						white-space: nowrap;
						overflow: hidden;
						text-overflow: ellipsis;
					}
					.search-item:hover {
						cursor: pointer;
						background-color: #EFEFEF;
					}
					.search-list-bottom {
						color: #BBBBBB;
						padding: 10px 20px;
						text-align: center;
					}
				}
			}
		}
	}
	
	/* 内容部分样式 */
	.el-main {
		//width: 1300px;
		//margin: 0px auto;
		width: 70%;
		margin: 0px auto;
		/* 回到顶部 */
		.up {
			height: 100%;
			width: 100%;
			text-align: center;
			line-height: 40px;
			color: #00C3FF;
		}
	}
	
	//用户导航栏
	.el-menu {
		background-color: transparent;
	}
	.el-menu.el-menu--horizontal {
		border-bottom: none !important;
	}
	.el-menu-item:focus, .el-menu-item:hover {
		background-color: rgba(255,255,255,0.3) !important;
	}
	
	/*小尺寸样式*/
	@media screen and (min-width: 1070px) {
		//输入框
		.search-input {
			width: 400px;
		}
	}
	
	@media screen and (max-width: 700px) {
		.el-main {
			width: 100% !important;
			padding: 20px;
		}
	}
	
	@media screen and (max-width: 1200px) and (min-width: 701px) {
		.el-main {
			width: 90% !important;
			padding: 20px;
		}
	}
</style>
