<template>
	<div class="comment-container">
		<!-- 暂无评论 -->
		<div class="no-comment" v-if="commentList.length === 0">
			<span>暂无评论</span>
		</div>
		<!-- 评论项 -->
		<div class="comment-list-item" v-for="comment in commentList">
			<header class="comment-header">
				<div class="comment-time">
					<i class="el-icon-date icon-margin-right-small"></i>
					<span>{{comment.commentTime}}</span>
				</div>
				<el-dropdown size="small">
					<i class="el-icon-more text-size-large"></i>
					<el-dropdown-menu slot="dropdown">
						<el-dropdown-item v-if="isHost" @click.native="handleDeleteComment(comment.commentId)">删除评论</el-dropdown-item>
					</el-dropdown-menu>
				</el-dropdown>
			</header>
			<!-- 评论内容 -->
			<section>
				<!-- 头部 -->
				<header class="comment-title">
					<el-tag effect="dark" size="mini" color="#00C3FF">{{comment.categoryName}}</el-tag>
					<h4 @click="routeToPostDetail(comment.parentId)">原帖：{{comment.title}}</h4>
				</header>
				<!-- 内容 -->
				<section>
					<p class="comment-content">评论内容：{{comment.content}}</p>
					<span v-for="image in comment.images">
						<img :src="image" width="100" class="icon-margin-right-small"/>
					</span>
				</section>
			</section>
		</div>
		<!-- 分页导航 -->
		<el-pagination :current-page.sync="commentQueryInfo.pageNum" :page-size="commentQueryInfo.pageSize" :total="commentQueryInfo.total"layout="total,prev, pager, next" @current-change="commentHandleCurrentChange"/>
	</div>
</template>

<script>
	import { getMyCommentList, deleteMyComment } from '@/api/user'
	import { responseStatus } from '@/utils/responseStatusUtil'
	export default {
		name: 'commentListComponent',
		props: { 
			userId: {type: Number, requires: true},
			isHost:  {type: Boolean, requires: true},
		},
		data() {
			return {
				commentList: [],
				commentQueryInfo: {
					pageNum: 1,
					pageSize: 10,
					total: 0
				}
			}
		},
		methods:{
			
			/* 获取评论列表数据 */
			handleGetMyCommentList(){
				const data = {
					userId: this.userId,
					pageNum: this.commentQueryInfo.pageNum,
					pageSize: this.commentQueryInfo.pageSize
				}
				getMyCommentList(data).then(res => {
					if(responseStatus(res)){
						this.commentQueryInfo.pageNum = res.data.data.pageNum;
						this.commentQueryInfo.pageSize = res.data.data.pageSize;
						this.commentQueryInfo.total = res.data.data.total;
						this.commentList = res.data.data.list; 
					}
							   	
				})
			},
			
			
			/**
			 * 删除评论
			 */
			handleDeleteComment(commentId) {
				const data = {
					commentId: commentId
				}
				this.$confirm('此操作将会删除该评论, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					deleteMyComment(data).then((res) => {
						if(responseStatus(res)){
							this.$message.success("删除成功!");
							this.handleGetMyCommentList();
						}				   							   
					})
				}).catch(() => {
					return
				})
			},	
			
			commentHandleCurrentChange(newPage){
				this.commentQueryInfo.pageNum = newPage;
				this.handleGetMyCommentList();
			},
			/* 跳转到帖子详情 */
			routeToPostDetail(postId){
				this.$router.push({path: '/postDetailPage',query: {postId: postId}});
			},
		},
		mounted() {
			this.handleGetMyCommentList();
		}
	}
</script>

<style lang="less" scoped="scoped">
	.comment-container {
		background-color: #FFF;
		border-radius: 10px;
		
		//暂无评论
		.no-comment {
			color: #808080;
			font-size: 14px;
			text-align: center;
			padding: 20px;
		}
		
		//评论项
		.comment-list-item {
			font-size: 14px;
			padding: 20px;
			border-bottom: 5px #F0F1F5 solid;
			.comment-header {
				display: flex;
				justify-content: space-between;
			}
			//评论标题
			.comment-title {
				display: flex;
				margin: 20px 0;
				.el-tag {
					font-size: 8px;
					font-weight: 600;
					border: none;
				}
				h4 {
					margin: 0px;
					margin-left: 10px;
					font-weight: 600;
				}
			}
			//评论时间
			.comment-time {
				color: #808080;
				font-size: 12px;
			}
			//评论内容
			.comment-content {
				color: #888;
				padding: 20px;
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis;
				border-radius: 10px;
				background-color: #F0F1F5;
			}
		}
	}
	
	//分页导航
	.el-pagination {
		text-align: center;
		margin-top: 10px;
		background-color: #FFF;
		padding: 10px;
		border-radius: 10px;
	}
</style>
