<template>
	<div>
		<header class="post-list-head">
			<el-button type="primary" @click="routeToPostingPage">发表帖子</el-button>
			<el-pagination class="pagination hidden-xs-only" :current-page.sync="postListInfo.pageNum" :page-size="postListInfo.pageSize" :total="postListInfo.total"
					layout="total, prev, pager, next, jumper" @current-change="handleCurrentChange" >
			</el-pagination>
		</header>
		<el-table :data="postList" border style="width: 100%" @row-click="handleOpenDetail">
		    <el-table-column prop="title" label="标题"/>
			<el-table-column prop="categoryName" label="分类">
				<template slot-scope="scope">
				　　　　	<span style="color: #FFA500;">{{scope.row.categoryName}}</span>
				</template>
			</el-table-column>
			<el-table-column prop="createTime" label="创建时间"/>
			<el-table-column prop="updateTime" label="最后更新"/>
			<el-table-column prop="commentNum" label="评论数"/>
			<el-table-column prop="isEssences" label="精华">
				<template slot-scope="scope">
						<el-tag v-if="scope.row.isEssences">精华</el-tag>
				　　　　	<el-tag v-else type="info">普通</el-tag>
				</template>
			</el-table-column>
			<el-table-column label="操作">
				<template slot-scope="scope">
					<el-tooltip effect="dark" content="删除帖子" placement="top" :enterable="false">
						<el-button type="danger" size="mini" icon="el-icon-delete" @click.stop="handleDeletePost(scope.row.postId)"></el-button>
					</el-tooltip>
				</template>
			</el-table-column>
		  </el-table>
		  <el-pagination class="pagination" :current-page.sync="postListInfo.pageNum" :page-size="postListInfo.pageSize" :total="postListInfo.total"
		  		layout="total, prev, pager, next, jumper" @current-change="handleCurrentChange" >
		  </el-pagination>
	</div>
</template>

<script>
	import { getPostListByUserId,deleteMyPost } from '@/api/user'
	import { responseStatus } from '@/utils/responseStatusUtil'
	export default {
		name: "postManageListComponent",
		data(){
			return {
				user: {},
				postListInfo: {
					pageNum: 1,
					pageSize: 10,
					total: 0
				},
				postList: []
			}
		},
		methods: {
			/* 获取用户的帖子 */
			handleGetMyPostList(){
				const data = {
					pageNum: this.postListInfo.pageNum,
					pageSize: this.postListInfo.pageSize
				}
				getPostListByUserId(data).then(res => {
					if(responseStatus(res)){
						this.postListInfo.pageNum = res.data.data.pageNum;
						this.postListInfo.pageSize = res.data.data.pageSize;
						this.postListInfo.total = res.data.data.total;
						this.postList = res.data.data.list; 
					}
				})
			},
			
		
			
			/** 删除帖子 */
			handleDeletePost(postId) {
				const data = [];
				data.push(parseInt(postId));
				deleteMyPost(data).then((res) => {
					if(responseStatus(res)){
						this.$message.success("删除成功");
						this.handleGetMyPostList();
					}				   							   
				})
			},
			
			handleOpenDetail(row){
				/* 跳转到帖子详情 */
				this.$router.push({path: '/postDetailPage',query: {postId: row.postId}});
			},
			
			/* 当前页数改变 */
			handleCurrentChange(newPage){
				this.postListInfo.pageNum = newPage;
				this.handleGetMyPostList();
			},
			
			/* 跳转到发表帖子界面 */
			routeToPostingPage(){
				if(this.user == null){
					return this.$message.warning("请先登录！")
				}
				this.$router.push('/postingPage');
			}
		},
		created() {
			this.user = JSON.parse(window.sessionStorage.getItem('user'));
			if(this.user == null){
				return this.$message.warning("请先登录！")
			}
			this.handleGetMyPostList();
		}
		
	}
</script>

<style scoped>
	.post-list-head {
		height: 40px;
		margin-bottom: 10px;
		display: flex;
		justify-content: space-between;
	}
	.pagination {
		margin: 10px 0;
		text-align: right;
	}
</style>
