<template>
	<div class="hot-category-container">
		<!-- 标题头部 -->
		<header class="title">
			<div>
				<i class="el-icon-menu"></i>
				<span>热门分类</span>
			</div>
			<span class="color-li-gray more">更多</span>
		</header>
		<!-- 内容部分 -->
		<section>
			<ul class="content">
				<li class="content-item" v-for="(item,index) in hotCategoryList" @click="routToCategoryPostPage(item.categoryId,item.categoryName)">
					<div>
						<img :src="item.categoryImage"/>
					</div>
					<div class="category-info">
						<span class="content-title">{{item.categoryName}}</span>
						<span class="color-li-gray text-size-small">帖子数：{{item.postNum}}</span>
					</div>	
				</li>
			</ul>
		</section>
	</div>
	
</template>

<script>
	import { getCategory } from '@/api/user'
	import { responseStatus } from '@/utils/responseStatusUtil'
	export default{
		data(){
			return {
				hotCategoryList: [],
				
			}
		},
		methods: {
			/* 取得热门分类 */
			handleGetHotCategoryList(){
				/* 获得分类列表 */
				getCategory().then(res => {
					if(responseStatus(res)){
						for(let i in res.data.data){
							if(res.data.data[i].isRecommend)
								this.hotCategoryList.push(res.data.data[i]);
						}
					}
				})
			},
			
			/* 通过分类id跳转到帖子列表 */
			routToCategoryPostPage(categoryId,categoryName){
					this.$router.push({path: '/subscribeCategoryPostListPage', query: { categoryId: categoryId,categoryName: categoryName}});
			}
		},
		mounted() {
			this.handleGetHotCategoryList();
		}
	}
</script>

<style lang="less" scoped>
	.hot-category-container {
		padding: 15px 10px;
		margin-top: 20px;
		background-color: #FFF;
		/* 标题头部 */
		.title {
			font-size: 14px;
			display: flex;
			align-items: center;
			justify-content: space-between;
			i {
				color: #FF4000;
				margin-right: 5px;
			}
			.more:hover {
				cursor: pointer;
				color: #00C3FF;
			}
		}
		/* 内容部分 */
		.content {
			font-size: 14px;
			margin-top: 20px;
			.content-item {
				display: flex;
				align-items: center;
				padding: 10px;
				img {
					height: 60px;
					width: 60px;
					border-radius: 10px;
					margin-right: 10px;
				}
				.category-info {
					span {
						display: block;
					}
				}
			}
			.content-item:hover {
				cursor: pointer;
				.content-title {
					color: #00C3FF;
				}
			}
		}
	}
</style>
