<template>
	<div class="post-list-container">
		<!-- 帖子列表 -->
		<el-card class="post-container">
			<div slot="header" style="font-size: 14px;">搜索{{searchKeyword}}的结果</div>
			<div class="post-content">
				<div class="post-item" v-for="item in searchResultList" @click="routeToPostDetail(item)">
					<el-avatar :size="40" :src="item.headSculpture"></el-avatar>
					<div class="post-info">
						<span style="display: block; margin-left: 10px;">{{item.title}}</span>
						<div class="post-info-bottom">
							<span class="post-info-user">楼主：{{item.userName}}</span>
							<span class="post-info-date">{{item.createTime}}</span>
							<span class="post-info-date">最后更新：{{item.updateTime}}</span>
						</div>
					</div>	
				</div>	
			</div>	
		</el-card>
		<div class="post-list-pagination">
			<el-pagination :current-page.sync="queryInfo.pageNum" :page-size="queryInfo.pageSize" :total="queryInfo.total"
				@current-change="handleCurrentChange" layout="prev, pager, next, jumper">
			</el-pagination>
		</div>
	</div>
</template>

<script>
	export default{
		name: 'searchResultPage',
		data(){
			return {
				queryInfo: {
					//当前页数
					pageNum: 1,
					pageSize: 10,
					total: 0
				},
				//传来的搜索关键字
				searchKeyword: this.$route.query.searchKeyword,
				//搜索结果
				searchResultList: []
			}
		},
		methods: {
			/**
			 * 处理页面变化
			 * @param {Object} newPage
			 */
			handleCurrentChange(newPage){
				console.log(newPage);
				this.queryInfo.pageNum = newPage;
				this.getSearchResultList(this.searchKeyword);
			},
			/**
			 * 获得搜索结果列表
			 * @param {Object} searchKeyword 搜索关键字
			 */
			getSearchResultList(searchKeyword){
				this.$axios.get('/searchByString', {
					params: { content: this.searchKeyword,
							  pageNum: this.queryInfo.pageNum,
							  pageSize: this.queryInfo.pageSize}})
						   .then(res => {
							   if(res.status !== 200)
							   	return this.$message.error("获取帖子失败!!!");
							   else if(res.data.status !== 10000)
							   	this.$message.error(res.data.msg+"!!!");
							   else{
									this.queryInfo = res.data.data;
							   		this.searchResultList = res.data.data.list; 
							   		console.log(this.searchResultList);
									console.log(this.queryInfo);
							   }
						   })
			},
			/**
			 * 跳转到详情页面
			 * @param {Object} post 帖子对象
			 */
			routeToPostDetail(post){
				const postStr = JSON.stringify(post);
				window.sessionStorage.setItem('postStr',postStr);
				this.$router.push('/forumDetailPage');
			}
		},
		created() {
			this.getSearchResultList(this.searchKeyword);
		}
	}
</script>

<style scoped="scoped">
	.post-list-container {
		min-width: 1310px;
		padding: 40px;
	}
	.post-container {
		min-width: 1250px;
		margin: 10px 0;
		border-top: 3px #68C7FF solid;
		border-radius: 5px;
	}
	.post-container {
		padding: 10px;
	}
	.post-item {
		display: flex;
		padding: 10px;
		border-bottom: 1px #EAEDF1 solid;
	}
	.post-item:hover {
		cursor: pointer;
		background-color: #EAEDF1;
	}
	.post-info-bottom {
		color: #888;
		font-size: 12px;
		padding-left: 10px;
		padding-top: 10px;
	}
	.post-info-date {
		margin-left: 50px;
	}
	.post-list-pagination {
		margin: 10px 0;
		text-align: right;
	}
</style>
