<template>
	<div>
		
		<!-- 分页导航 -->
		<div class="pagination-head">
			<el-button id="posting-button" @click="routeToPostingPage">发新帖</el-button>
			<el-pagination class="hidden-xs-only" :current-page.sync="queryInfo.pageNum" :page-size="queryInfo.pageSize" :total="queryInfo.total"
				layout="total, prev, pager, next, jumper" @current-change="handleCurrentChange">
			</el-pagination>
		</div>
		
		<!-- 帖子列表 -->
		<subscribe-category-post-list :subscribeCategoryPostList="subscribeCategoryPostList" :total="queryInfo.total" :title="categoryName" :categoryId="categoryId" :isSign="isSign"></subscribe-category-post-list>
		
		<!-- 分页导航 -->
		<div class="post-list-pagination">
			<el-pagination :current-page.sync="queryInfo.pageNum" :page-size="queryInfo.pageSize" :total="queryInfo.total"
					layout="prev, pager, next, jumper" @current-change="handleCurrentChange">
			</el-pagination>
		</div>
	</div>
</template>

<script>
	import subscribeCategoryPostListComponent from './home_components/subscribeCategoryPostListComponent.vue'
	import { getCategoryPostList } from '@/api/user'
	import { responseStatus } from '@/utils/responseStatusUtil'
	export default{
		name: 'subscribeCategoryPostListPage',
		data(){
			return {
				queryInfo: {
					//当前页数
					pageNum: 1,
					pageSize: 10,
					total: 0
				},
				subscribeCategoryPostList: [],
				categoryName: this.$route.query.categoryName,
				categoryId: this.$route.query.categoryId,
				isSign: this.$route.query.isSign,
				//帖子排序规则
				order: 0
			}
		},
		methods: {
			/**
			 * 获得帖子列表
			 */
			handleGetCategoryPost(){
				const data = {
					categoryId: this.categoryId,
					pageNum: this.queryInfo.pageNum,
					pageSize: this.queryInfo.pageSize,
					orderBy: this.order
				}
				getCategoryPostList(data).then(res => {
					if(responseStatus(res)){
						this.queryInfo = res.data.data;
						this.subscribeCategoryPostList = res.data.data.list; 
					}
				})
			},
			
			
			
			/**
			 * 当前页数改变
			 * @param {Object} newPage
			 */
			handleCurrentChange(newPage){
				this.queryInfo.pageNum = newPage;
				this.handleGetCategoryPost();
			},
			
			/**
			 * 跳转到详情页面
			 * @param {Object} post 帖子对象
			 */
			routeToPostDetail(post){
				const postStr = JSON.stringify(post);
				window.sessionStorage.setItem('postStr',postStr);
				this.$router.push('/forumDetailPage');
			},
			
			/**
			 * 跳转到写帖子界面
			 */
			routeToPostingPage(){
				this.$router.push('/postingPage');
			}
		},
		components:{
			subscribeCategoryPostList: subscribeCategoryPostListComponent
		},
		created(){
			this.handleGetCategoryPost();
		}
	}
</script>

<style scoped="scoped">
	
	.pagination-head {
		margin: 10px 0;
		display: flex;
		justify-content: space-between;
	}
	
	#posting-button {
		color: #FFF;
		background-color: #00C3FF; 
		margin-left: 5px;
		border: none;
	}
	
	.post-list-pagination {
		margin: 10px 0;
		text-align: right;
	}
</style>
