<template>
	<div>
		<ul>
			<li v-for="newComment in newCommentsList" class="new-comments-list-item" @click="routeToPostDetail(newComment.postId)">	
				<p>{{newComment.title}}</p>
				<span>{{newComment.updateTime}}</span>
				<span>{{newComment.content}}</span>
			</li>
			<li v-if="newCommentsList.length === 0" class="new-comments-list-no-item">
				暂无新的评论
			</li>
		</ul>
		<el-pagination :current-page.sync="pageInfo.pageNum" :page-size="pageInfo.pageSize" :total="pageInfo.total"
			layout="total, prev, pager, next" @current-change="handleCurrentChange" style="text-align: right;">
		</el-pagination>
	</div>
	
</template>

<script>
	import { getNewComments } from '../../../api/user'
	import { responseStatus } from '../../../utils/responseStatusUtil'
	export default {
		name: "newCommentComponent",
		data(){
			return {
				newCommentsList: [],
				pageInfo: {
					pageNum: 1,
					pageSize: 10,
					total: 0
				},
				loading: false
			}
		},
		methods: {
			handleGetNewComments(){
				const data = {
					pageNum: this.pageInfo.pageNum,
					pageSize: this.pageInfo.pageSize
				}
				getNewComments(data).then(res => {
					if(responseStatus(res)){
						this.pageInfo = res.data.data;
						this.newCommentsList = res.data.data.list;
					}
				})
			},
			
			/**
			 * 分页控制
			 * @param {Object} newPage
			 */
			handleCurrentChange(newPage){
				this.pageInfo.pageNum = newPage;
				this.handleGetNewComments();
			},
			
			
			/**
			* 跳转控制
			* @param {Object} post 帖子对象
			 */
			routeToPostDetail(postId){
				this.$router.push({path: '/postDetailPage',query: {postId: postId}});
			}
		},
		created() {
			this.handleGetNewComments();
		}
	}
</script>

<style scoped>
	.new-comments-list-no-item {
		color: #808080;
		font-size: 14px;
		text-align: center; 
	}
	.new-comments-list-item {
		padding: 10px;
		font-size: 14px;
	}
	.new-comments-list-item:hover {
		color: #409EFF;
		cursor: pointer;
		background-color: #EAEDF1;
	}
	 .new-comments-list-item p {
		max-width: 200px;
		display: inline-block;
		white-space: nowrap;
		overflow: hidden;
		text-overflow:ellipsis;
	}
	.new-comments-list-item span:nth-child(2) {
		float: right;
		color: #808080;
		font-size: 12px;
		margin-right: 20px;
	}
	.new-comments-list-item span:nth-child(3) {
		width: 100%;
		color: #808080;
		font-size: 12px;
		display: inline-block;
		white-space: nowrap; 
		overflow: hidden;
		text-overflow:ellipsis;
		margin-top: 15px;
	}
	.new-comments-list-bottom {
		color: #BBBBBB;
		font-size: 14px;
		text-align: center;
		padding-top: 10px;
	}
</style>
