<template>
	<div>
		<!-- 头部内容 -->
		<el-row :gutter="30" class="main-header">
			<el-col :xs="24" :sm="24" :md="24" :lg="17" :xl="17">
				<!-- 轮播图 -->
				<el-carousel class="banner hidden-xs-only" type="card" height="250px">
					<el-carousel-item v-for="item in bannerList">
							<el-image class="banner-image" :src="item.recommendImage" @click="routeToPostDetailPage(item.postId)"/>
					</el-carousel-item>
				</el-carousel>
				<el-carousel class="banner hidden-sm-and-up">
					<el-carousel-item v-for="item in bannerList">
							<el-image class="banner-image" :src="item.recommendImage" @click="routeToPostDetailPage(item.postId)"/>
					</el-carousel-item>
				</el-carousel>
			</el-col>
			<el-col :xs="24" :sm="24" :md="24" :lg="7" :xl="7">
				<!-- 天气插件 -->
				<div id="he-plugin-standard"></div>
			</el-col>
		</el-row>
		
		<!-- 关注的分类 -->
		<div v-if="user.userId">
			<!-- 分页导航 -->
			<div class="subscribe-container">
				<el-button id="posting-button" @click="routeToPostingPage">发新帖</el-button>
			</div>
			<!-- 关注的分类 -->
			<subscribeCategoryComponent></subscribeCategoryComponent>
			<!-- 关注的动态 -->
			<subscribePostComponent :queryInfo="pageInfo" :postList="mySubscribePostList" :title="'关注的帖子'"></subscribePostComponent>
		
			<!-- 分页导航 -->
			<div class="post-list-pagination">
				<el-pagination :current-page.sync="pageInfo.pageNum" :page-size="pageInfo.pageSize" :total="pageInfo.total"
						layout="prev, pager, next, jumper" @current-change="handleCurrentChange">
				</el-pagination>
			</div>
		</div>
		
		<!-- 底部内容 -->
			<el-row :gutter="30" class="main-recommend">
				<el-col :xs="24" :sm="24" :md="16" :lg="17" :xl="17">
					<recommend-post-list-component class="recommend-list"></recommend-post-list-component>
				</el-col>
				<el-col :xs="24" :sm="24" :md="8" :lg="7" :xl="7">
					<div class="recommend-menu">
						<!-- 下载手机版 -->
						<div class="app-download">
							<div>
								<img src="~@/assets/qr-img/app.png" width="100px"/>
							</div>
							<div class="app-title">
								<p class="color-li-blue">扫码下载手机论坛APP</p>
								<span class="color-li-gray">关注论坛最新动态</span>
							</div>
						<!-- 外部网站 -->
						</div>
							<a href="http://cet.neea.edu.cn/">
								<div class="website">
									<span class="web-title">四六级报名入口</span>
								</div>
							</a>
						<hot-post-list-component></hot-post-list-component>
						<hot-category-list-component></hot-category-list-component>
					</div>
				</el-col>
			</el-row>
		<!-- 底部内容————结束 -->

		
		
		
		
					
	</div>
	
</template>

<script>
	import subscribePostComponent from './home_components/subscribePostComponent.vue'
	import subscribleCategoryComponent from './home_components/subscribeCategoryComponent.vue'
	import HotPostListComponent from './home_components/HotPostListComponent.vue'
	import HotCategoryListComponent from './home_components/HotCategoryListComponent.vue'
	import HotUserComponent from './home_components/HotUserComponent.vue'
	import RecommendPostListComponent from './home_components/RecommendPostListComponent.vue'
	import { getUser,getSubscribePostList,getBanner } from '@/api/user.js'
	import { responseStatus } from '@/utils/responseStatusUtil'
	export default {
		name: 'homePage',
		data(){
			return {
				user: {},
				bannerList: [],
				//分页信息
				pageInfo: {
					pageNum: 1,		//当前页数
					pageSize: 10,	//分页大小
					total: 0		//总条数
				},
				//用户关注的用户的帖子
				mySubscribePostList: [],
				commentNum: [],
				
			}
		},
		methods: {
			
			/**
			 * 从网络获取用户信息
			 */
			handleGetUser(){
				if(window.localStorage.getItem('accessToken') != null){
					getUser().then(res => {
						if(responseStatus(res)){
							this.user = res.data.data; 	
						}
					})
				}	
			},
			
			/* 获取轮播图数据 */
			handleGetBannerList(){
				getBanner().then(res => {
					if(responseStatus(res)){
						this.bannerList = res.data.data;
					}
				})
			},
			
			/**
			 * 获取用户关注的帖子列表
			 */
			handleSubscribePost(){
				if(this.user.userId){
					const data = {
						pageNum: this.pageInfo.pageNum,
						pageSize: this.pageInfo.pageSize
					}
					getSubscribePostList(data).then(res => {
						if(responseStatus(res)){
							this.pageInfo = res.data.data;
							this.mySubscribePostList = res.data.data.list;
						}
					})
				}
				
			},
			
			/* 分页处理 */
			handleCurrentChange(newPage){
				this.pageInfo.pageNum = newPage;
				this.handleSubscribePost();
			},
			
			/* 跳转到帖子详情 */
			routeToPostDetailPage(postId){
				if(postId != null)
					this.$router.push({path: '/postDetailPage',query: {postId: postId}});
			},
			
			/* 跳转到发表帖子页面 */
			routeToPostingPage(){
				this.$router.push('/postingPage');
			},
			
			

		},
		components: {
			HotPostListComponent,
			HotCategoryListComponent,
			HotUserComponent,
			RecommendPostListComponent,
			subscribeCategoryComponent: subscribleCategoryComponent,
			subscribePostComponent: subscribePostComponent
		},
		mounted() {
			
			window.WIDGET = {"CONFIG": {
										"layout": "2",
										"width": "300",
										"height": "250",
										"background": "5",
										"dataColor": "FFFFFF",
										"borderRadius": "5",
										"key": "f809422e419c4d9db654e5760e29af50"
									  }}
			var script = document.createElement('script');
			script.type = 'text/javascript';
			script.src = 'https://widget.qweather.net/standard/static/js/he-standard-common.js?v=2.0';
			document.getElementsByTagName('head')[0].appendChild(script);
			this.$nextTick(function () {
				window.addEventListener('width', this.getWidth)
			});
		},
		created() {
			this.handleGetUser();
			this.handleGetBannerList();
			this.handleSubscribePost();
		}
	}
</script>
<style lang="less" scoped>
	/* 头部内容 */
	.main-header {
		
		//轮播图
		.banner {
			.banner-image {
				width: 100%;
				height: 100%;
				border-radius: 10px;
			}
		}
	}
	
	/* 中部内容 */
	.main-recommend {
		margin-top: 20px;
		
		//推荐列表
		.recommend-list {
			border-radius: 5px;
		}
		
		//推荐菜单
		.recommend-menu {
			//手机APP
			.app-download {
				display: flex;
				align-items: center;
				padding: 20px 15px;
				margin-bottom: 20px;
				border-radius: 5px;
				background-color: #FFF;
				.app-title {
					margin-left: 10px;
				}
			}
			//学院官网
			.website {
				width: 100%;
				height: 180px;
				margin-bottom: 20px;
				border-radius: 10px;
				background-image: url(~@/assets/images/website/website.jpg);
				background-repeat: no-repeat;
				background-size: cover;
				position: relative;
				
				.web-title {
					color: #FFF;
					font-size: 14px;
					padding: 10px 20px;
					border-radius: 10px;
					display: flex;
					align-items: baseline;
					justify-content: center;
				}
				.web-title:hover {
					background-color: rgba(0,0,0,0.5);
				}
			}
		}
		
	}
	
	
	
	.subscribe-container {
		margin: 10px 0;
		display: flex;
		justify-content: space-between;
	}
	
	#posting-button {
		color: #FFF;
		background-color: #00C3FF; 
		margin-left: 5px;
		border: none;
	}
	
	.post-list-pagination {
		margin: 10px 0;
		text-align: right;
		margin-right: 40px;
	}
	
	@media screen and (max-width: 1200px) {
		.main-header {
			display: flex;
			flex-direction: column-reverse;
		}
	}
	
	@media screen and (max-width: 992px) {
		.main-recommend {
			display: flex;
			flex-direction: column-reverse;
		}
	}
</style>
