<template>
	<ul>
		<li class="subcribe-category-title">
			关注的分类
		</li>
		<li class="subcribe-category-item" v-for="item in subscribeCategoryList" @click="routToPostPage(item.categoryId,item.categoryName,item.isSign)">
			<span>{{item.categoryName}}</span>
			<span>{{item.postNum}}</span>
			<i class="el-icon-close" v-if="isEdit" @click.stop="handleDeleteSubscribeCategory(item.categoryId)"></i>
		</li>
		<li class="subcribe-edit" @click="isEdit = !isEdit">
			<span v-if="!isEdit">编辑</span>	
			<span v-else>取消编辑</span>	
		</li>
	</ul>	
</template>

<script>
	import { getSubscribeCategoryList,deleteSubscribeCategory } from '@/api/user'
	import { responseStatus } from '@/utils/responseStatusUtil'
	export default {
		name: "subscribeCategoryComponent",
		data(){
			return {
				subscribeCategoryList: [],
				isEdit: false,
				isOpen: false,
			}
		},
		methods: {
			
			/* 获取关注的分类列表 */
			handleSubscribeCategory(){
				getSubscribeCategoryList().then(res => {
					if(responseStatus(res))
						this.subscribeCategoryList = res.data.data;
				})
			},

			/* 取消关注分类 */
			handleDeleteSubscribeCategory(categoryId){
				const data = {
					categoryId: categoryId
				}
				deleteSubscribeCategory(data).then(res => {
					if(responseStatus(res)){
						this.$message.success("取消成功!!!");
						this.handleSubscribeCategory();
						this.isEdit = false;
					}
				})
			},
			
			/* 通过分类id跳转到帖子列表 */
			routToPostPage(categoryId,categoryName,isSign){
				this.$router.push({path: '/subscribeCategoryPostListPage', query: { categoryId: categoryId,categoryName: categoryName, isSign:isSign}});
			}
		},
		mounted() {
			this.handleSubscribeCategory();
		}
	}
</script>

<style scoped>
	.category-container {
		display: flex;
		justify-content: space-between;
	}
	li {
		font-size: 12px;
		margin: 5px;
		padding: 8px 15px;
		display: inline-block;
		border-radius: 5px;	
	}
	.subcribe-category-item:hover {
		color: #FFF;
		cursor: pointer;
		background-color: #00C3FF;
	}
	.subcribe-category-item:hover > span:nth-child(2) {
		color: #282828;
		cursor: pointer;
		background-color: #FFF;
	}
	
	.subcribe-category-item span:nth-child(2){
		color: #FFF;
		padding: 0 10px;
		margin: 0 5px;
		border-radius: 20px;	
		background-color: #00C3FF;
	}
	.subcribe-category-title {
		color: #FFF;
		background-color: #00C3FF;
	}
	.subcribe-edit {
		color: #FFF;
		background-color: #00C3FF;
		cursor: pointer;
	}
</style>
