<template>
	<div class="login-container">
		<div class="login-box">
			<div class="login-title">欢 迎 来 到 论 坛</div>
			<el-form ref="loginForm" :model="loginForm" :rules="loginRules">
				<el-form-item prop="username">
					<el-input type="text" placeholder="请输入用户名" v-model="loginForm.username" prefix-icon="el-icon-user"/>
				</el-form-item>
				<el-form-item prop="password">
					<el-input type="password" placeholder="请输入密码" v-model="loginForm.password" prefix-icon="el-icon-lock" @keyup.enter.native="handleLogin"/>
				</el-form-item>
				<el-button size="small" type="primary" style="width: 100%;" @click="handleLogin">登 录</el-button>
				<div class="bottom-item">
					<router-link to="/register" style="color: #409EFF;">
						<span>注册用户</span>
					</router-link>
					<span> | </span>
					<router-link to="/forgetPassword" style="color: #409EFF;">
						<span>忘记密码</span>
					</router-link>
				</div>
			</el-form>
		</div>
	</div>
</template>

<script>
import { login } from '../api/login'
import { getUser } from '../api/user' 
import { responseStatus } from '../utils/responseStatusUtil'
export default {
	name: 'login',
	data(){
		return {
			loginForm: {
				username: '',
				password: ''
			},
			/*验证规则对象*/
			loginRules: {
				username: [{required: true, message:"请输入用户名", trigger:'blur'}],
				password: [{required: true, message: '请输入密码', trigger:'blur'}]
			}
		}
	},
	methods: {
		handleLogin() {	
			this.$refs.loginForm.validate(valid => {
				const data = {
					username: this.loginForm.username,
					password: this.loginForm.password
				}
				if(valid){
					login(data).then(res => {
						if(responseStatus(res)){
							window.localStorage.setItem('accessToken',res.data.data);
							getUser().then(res => {
								if(responseStatus(res)){
									window.sessionStorage.setItem('user',JSON.stringify(res.data.data));
									this.$router.push('/home');
								}
							})	
						}	
					})		
				}
			})	
		},
		loginWithLocalStorage(){
			if(window.localStorage.getItem('accessToken')){
				getUser().then(res => {
					if(responseStatus(res)){
						window.sessionStorage.setItem('user',JSON.stringify(res.data.data));
						this.$router.push('/home');
					}
				})
			}
		}
	},
	created() {
		this.loginWithLocalStorage();
	}
}
</script>

<style scoped="scoped">
	.login-container {
		width: 100%;
		height: 100vh;
		display: flex;
		justify-content: center;
		align-items: center;
		font-size: 12px;
		color: #707070;
		background: url('../assets/login_images/background.jpg');
		background-size: cover;
	}
	.login-box {
		width: 320px;
		padding: 15px 30px 20px;
		background: #fff;
		border-radius: 10px;
		box-shadow: 0 15px 30px 0 rgba(0, 0, 1, .1);
	}
	.login-title {
	  margin: 20px auto;
	  text-align: center;
	  font-size: 18px;
	}
	.bottom-item {
		float: right;
		margin-top: 20px;
	}
</style>
