<template>
	<div v-if="userInfo.userId != null">
		<el-dropdown  @command="handleCommand" >
			<el-avatar :size="40" :src="userInfo.headSculpture"></el-avatar>
			<el-dropdown-menu slot="dropdown" style="padding: 0px;">
				<el-dropdown-item icon="el-icon-message" command="/messagePage">
					消息
					<el-badge :value="messageNum" :max="99" :hidden="badgeHidden"></el-badge>
					</el-dropdown-item>
					<el-dropdown-item icon="el-icon-user" command="/setting">设置</el-dropdown-item>
				<el-dropdown-item icon="el-icon-user" command="/userCenter">个人中心</el-dropdown-item>
				<el-dropdown-item icon="el-icon-view" command="/adminHome" v-if="userInfo.role == 2">管理界面</el-dropdown-item>
				<el-dropdown-item icon="el-icon-switch-button" @click.native="handleLogout">退出登录</el-dropdown-item>
			</el-dropdown-menu>
		</el-dropdown>
	</div>
	
	<div v-else>
		<el-dropdown @command="handleCommand">
			<el-avatar class="avatar" :size="40" :fit="'cover'">登录</el-avatar>
			<el-dropdown-menu slot="dropdown" style="padding: 0px;">
				<el-dropdown-item icon="el-icon-user" command="/login">登录</el-dropdown-item>
				<el-dropdown-item icon="el-icon-message" command="/register">注册</el-dropdown-item>
			</el-dropdown-menu>
		</el-dropdown>
	</div>
	
</template>

<script>
	import { logout } from '@/api/login'
	import { getMessageNum } from '@/api/user'
	import { responseStatus } from '@/utils/responseStatusUtil'
	export default {
		name: 'avatarComponent',
		inject: ['reload'],
		props: { userInfo: { type: Object, requires: true },
				 messageNum: { type: Number, requires: true }},
		computed: {
			badgeHidden: function(){
				return this.messageNum == 0;
			}
		},
		methods:{
			handleLogout(){
				logout();
				window.sessionStorage.clear();
				window.localStorage.clear();
				this.userInfo.userId = null;
				clearInterval(this.$timerCount[0]);
				this.reload();
				this.$router.push('/homePage');
			},
			
			/**
			 * 下拉菜单处理事件
			 * @param {Object} command 菜单地址
			 */
			handleCommand(command){
				if(command == '/adminHome')
					clearInterval(this.$timerCount[0]);
				this.$router.push(command);
				this.reload();
			}
		
		}
	}
</script>

<style scoped>
	.avatar {
		background-color: #FFF;
		color: #00a1d6;
		cursor: pointer;
	}
</style>
