<template>
	<div class="fans-box">
		<!-- 粉丝列表项 -->
		<div class="fans-item" v-for="item in fansList">
			<!-- 粉丝头像 -->
			<div>
				<el-avatar :size="70" :src="item.headSculpture"/>
			</div>
			<!-- 粉丝基本信息 -->
			<div class="information">
				<span class="username" @click="routeToUserCenter(item.userId)">{{item.username}}</span>
				<span class="color-li-gray">{{item.personalizedSignature ? item.personalizedSignature : "系统原装签名"}}</span>
				<span class="color-li-gray">最近更新：{{item.updateTime}}</span>	
			</div>
			<!-- 关注粉丝按钮 -->
			<div class="follow-operation" v-if="isHost">
				<el-button size="small" v-if="item.subscribeStatus == 0" plain @click.stop="handleFollow(item.userId)">关注</el-button>
				<span v-if="item.subscribeStatus == 1" @click.stop="handleCancelFollow(item.userId)">已关注</span>
				<span v-if="item.subscribeStatus == 2" @click.stop="handleCancelFollow(item.userId)">已相互关注</span>
			</div>
		</div>
		<div class="no-fans" v-if="fansListPage.total == 0">
			<span class="color-li-gray">还没有粉丝</span>
		</div>
		<!-- 分页导航 -->
		<div class="fans-pagination">
			<el-pagination :current-page.sync="fansListPage.pageNum" :page-size="fansListPage.pageSize" :total="fansListPage.total"
					layout="total, prev, pager, next" @current-change="handleFansListCurrentChange">
			</el-pagination>
		</div>
	</div>
</template>

<script>
	import { getFansList, notFollow, follow } from '@/api/user'
	import { responseStatus } from '@/utils/responseStatusUtil'
	export default {
		inject: ['reload'],
		props: {
			userId: { type: Number, requires: true },
			isHost: { type: Boolean, requires: false }
		},
		data(){
			return {
				fansListPage: {
					pageNum: 0,
					pageSize: 10,
					total: 0
				},
				fansList: [],
			}
		},
		methods: {
			
			/* 获取粉丝列表 */
			handleGetFansList(){
				const data = {
					beSubscribe: this.userId,
					pageNum: this.fansListPage.pageNum,
					pageSize: this.fansListPage.pageSize
				}
				getFansList(data).then(res => {
					if(responseStatus(res)){
						this.fansListPage.pageNum = res.data.data.pageNum;
						this.fansListPage.pageSize = res.data.data.pageSize;
						this.fansListPage.total = res.data.data.total;
						this.fansList = res.data.data.list;
					}
				})
			},
			
			/* 关注用户 */
			handleFollow(userId) {
				const data = {
					beSubscribe: userId
				}
				if(data.beSubscribe == null){
					data.beSubscribe = this.user.userId
				}
				follow(data).then(res => {
					if(responseStatus(res)){
						this.handleGetFansList();
						return this.$message.success("关注成功!");
					}
				});
			},
			
			
			/* 取消关注 */
			handleCancelFollow(userId) {
				const data = {
					beSubscribe: userId
				}
				this.$confirm('此操作将取消对该用户的关注, 是否继续?', '提示', {
						confirmButtonText: '确定',
						cancelButtonText: '取消',
						type: 'warning'
				}).then(() => {
					notFollow(data).then((res) => {
						if(responseStatus(res)){
				        	this.handleGetFansList();
				        	this.$message.success("取消成功");			   
				        }					
					});
				}).catch(() => {
					return         
				});
			},
			
			/* 粉丝列表改变 */
			handleFansListCurrentChange(newPage){
				this.fansListPage.pageNum = newPage;
				this.handleGetFansList();
			},
			
			/* 跳转到其他用户的信息界面 */
			routeToUserCenter(userId){
				this.$router.push({path: '/userCenter',query: {userId: userId}});
				this.reload();
			}
		},
		mounted() {
			this.handleGetFansList();
		}
	}
</script>

<style lang="less" scoped>
	/* 粉丝列表组件 */
	.fans-box {
		font-size: 14px;
		//粉丝列表项
		.fans-item {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			padding: 15px 20px;
			border-radius: 10px;
			border-bottom: 1px #EBEBEB solid;
			//粉丝信息
			.information {
				margin-left: 20px;
				flex-grow: 1;
				span {
					display: block;
				}
				.username:hover {
					cursor: pointer;
					color: #00C3FF;
				}
			}
			//关注按钮
			.follow-operation {
				color: #999;
				margin-right: 20px;
				&:hover {
					cursor: pointer;
					color: #00C3FF;
				}
				.el-button {
					color: #00C3FF;
					border-color: #00C3FF;
				}
			}
		}
		//没有粉丝样式
		.no-fans {
			text-align: center;
			padding: 20px 0;
		}
		//底部页面导航
		.fans-pagination {
			padding: 10px;
			text-align: center;
		}
	}
	
	
	@media screen and (max-width: 556px) {
		.fans-item {
			align-items: flex-start !important;
			flex-direction: column !important;
		}
		.information {
			margin-left: 0 !important;
		}
	}
	
	
	
</style>
