<template>
	<div class="follow-box">
		<div class="follow-item" v-for="item in followList">
			<div>
				<el-avatar :size="70" :src="item.headSculpture"/>
			</div>
			<div class="information">
				<span class="username" @click="routeToUserCenter(item.userId)">{{item.username}}</span>
				<span class="color-li-gray">{{item.personalizedSignature ? item.personalizedSignature : "系统原装签名"}}</span>
				<span class="color-li-gray">最近更新：{{item.updateTime}}</span>	
			</div>
			<div class="follow-operation" v-if="isHost">
				<el-button v-if="item.subscribeStatus == 0" size="small" plain  @click.stop="handleFollow(item.userId)">关注</el-button>
				<span v-if="item.subscribeStatus == 1" @click.stop="handleCancelFollow(item.userId)">已关注</span>
				<span v-if="item.subscribeStatus == 2" @click.stop="handleCancelFollow(item.userId)">已相互关注</span>
			</div>
		</div>
		<div class="no-follow" v-if="followInfoPage.total == 0">
			<span class="color-li-gray">还没有关注的人</span>
		</div>
		<!-- 分页导航 -->
		<div class="follow-pagination">
			<el-pagination :current-page.sync="followInfoPage.pageNum" :page-size="followInfoPage.pageSize" :total="followInfoPage.total"
					layout="total, prev, pager, next" @current-change="handleFollowCurrentChange">
			</el-pagination>
		</div>
	</div>
	
</template>

<script>
	import { getFollowList, notFollow, follow } from '@/api/user'
	import { responseStatus } from '@/utils/responseStatusUtil'
	export default {
		inject: ['reload'],
		props: {
			userId: { type: Number, requires: true },
			isHost: { type: Boolean, requires: false }
		},
		data(){
			return {
				followInfoPage: {
					//当前页数
					pageNum: 1,
					pageSize: 10,
					total: 0
				},
				followList: [],
			}
		},
		methods: {
			
			/* 获取关注列表 */
			handleGetFollowList(userId){
				const data = {
					subscribe: userId,
					pageNum: this.followInfoPage.pageNum,
					pageSize: this.followInfoPage.pageSize
				}
				getFollowList(data).then(res => {
					if(responseStatus(res)){
						this.followInfoPage.pageNum = res.data.data.pageNum;
						this.followInfoPage.pageSize = res.data.data.pageSize;
						this.followInfoPage.total = res.data.data.total;
						this.followList = res.data.data.list; 
					}
				})
			},
			
			/* 关注用户 */
			handleFollow(userId) {
				const data = {
					beSubscribe: userId
				}
				if(data.beSubscribe == null){
					data.beSubscribe = this.user.userId
				}
				follow(data).then(res => {
					if(responseStatus(res)){
						this.handleGetFollowList(this.userId);
						return this.$message.success("关注成功!");
					}
				});
			},
			
			/* 取消关注 */
			handleCancelFollow(userId) {
				const data = {
					beSubscribe: userId
				}
				this.$confirm('此操作将取消对该用户的关注, 是否继续?', '提示', {
						confirmButtonText: '确定',
						cancelButtonText: '取消',
						type: 'warning'
				}).then(() => {
					notFollow(data).then((res) => {
						if(responseStatus(res)){
				        	this.handleGetFollowList();
				        	this.$message.success("取消成功");			   
				        }					
					});
				}).catch(() => {
					return         
				});
			},
			
			/* 当前页数改变  */
			handleFollowCurrentChange(newPage){
				this.followInfoPage.pageNum = newPage;
				this.handleGetFollowList();
			},
			
			/* 跳转到其他用户的信息界面 */
			routeToUserCenter(userId){
				this.$router.push({path: '/userCenter',query: {userId: userId}});
				this.reload();
			}
		},
		mounted() {
			this.handleGetFollowList(this.userId);
		}
	}
</script>

<style lang="less" scoped>

	.follow-box {
		font-size: 14px;
		//关注项
		.follow-item {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			padding: 15px 20px;
			border-bottom: 1px #EBEBEB solid;
			border-radius: 10px;
			//关注信息
			.information {
				margin-left: 20px;
				flex-grow: 1;
				span {
					display: block;
				}
				.username:hover {
					cursor: pointer;
					color: #00C3FF;
				}
			}
			//关注按钮
			.follow-operation {
				color: #999;
				margin-right: 20px;
				&:hover {
					cursor: pointer;
					color: #00C3FF;
				}
				.el-button {
					color: #00C3FF;
					border-color: #00C3FF;
				}
			}
		}
		//没有关注样式
		.no-follow {
			text-align: center;
			padding: 20px 0;
		}
		//底部页面导航
		.follow-pagination {
			padding: 10px;
			text-align: center;
		}
	}
	
	@media screen and (max-width: 556px) {
		.follow-item {
			align-items: flex-start !important;
			flex-direction: column !important;
		}
		.information {
			margin-left: 0 !important;
		}
	}
</style>
