<template>
	<div>
		<!-- 头部导航 -->
		<el-breadcrumb separator="/" class="admin-nav">
		  <el-breadcrumb-item :to="{ path: '/adminHome' }">首页</el-breadcrumb-item>
		  <el-breadcrumb-item>文章管理</el-breadcrumb-item>
		  <el-breadcrumb-item>文章列表</el-breadcrumb-item>
		</el-breadcrumb>
		<!-- 卡片视图区域 -->
		<el-card class="post-list-card">
			<div>
				<div class="search-item">
					<el-input placeholder="请输入内容" v-model="keyword">
						<template slot="append">
							<span>搜索</span>
						</template>
					</el-input>
				</div>
				<!-- 分类列表 -->
				<el-table :data="postList" style="width: 100%">
					<el-table-column prop="postId" label="文章ID"/>
					<el-table-column prop="title" label="文章主题"/>
					<el-table-column prop="categoryName" label="分类"/>
					<el-table-column prop="isEssences" label="精化帖">
						<template slot-scope="scope">
						　　　　	<span v-if="scope.row.isEssences">精华</span>
								<span v-else>普通</span>
						</template>
					</el-table-column>
					<el-table-column label="操作">
						<template slot-scope="scope">
							<el-tooltip effect="dark" content="删除" placement="top" :enterable="false">
								<el-button type="danger" size="mini" icon="el-icon-delete" @click="handleDeletePost(scope.row.postId)"></el-button>
							</el-tooltip>
							<el-tooltip v-if="!scope.row.isEssences" effect="dark" content="设为精华" placement="top" :enterable="false">
								<el-button type="primary" size="mini" icon="el-icon-plus" @click="handleAddEssences(scope.row.postId,1)"></el-button>
							</el-tooltip>
							<el-tooltip v-else effect="dark" content="取消精华" placement="top" :enterable="false">
								<el-button size="mini" icon="el-icon-close" @click="handleAddEssences(scope.row.postId,0)"></el-button>
							</el-tooltip>
							<el-tooltip effect="dark" content="首页展示" placement="top" :enterable="false">
								<el-button type="warning" size="mini" icon="el-icon-picture-outline" @click="routeToBannerPage(scope.row.postId)"></el-button>
							</el-tooltip>
						</template>
					</el-table-column>
				</el-table>
			</div>
		</el-card>
		<!-- 分页区域 -->
		<el-pagination class="pagination" :current-page.sync="queryInfo.pageNum" :page-size="queryInfo.pageSize" :total="queryInfo.total"
				layout="total, prev, pager, next, jumper" @current-change="handleCurrentChange">
		</el-pagination>
	</div>
</template>

<script>
	import { getPostList, addEssences } from '@/api/admin'
	import { deleteMyPost, getSearchList } from '@/api/user'
	import { responseStatus } from '@/utils/responseStatusUtil'
	export default {
		name: 'adminPostList',
		data(){
			return {
				postList:[],
				//分页信息
				queryInfo: {
					pageNum: 1,		
					pageSize: 10,	
					total: 0		
				},
				keyword: ''
			}
		},
		methods:{
			/**
			 * 获取文章列表
			 */
			handleGetPostList(){
				const data = {
					pageNum: this.queryInfo.pageNum,
					pageSize: this.queryInfo.pageSize
				}
				getPostList(data).then((res) => {
					if(responseStatus(res)){
						this.queryInfo = res.data.data;
						this.postList = res.data.data.list;
						console.log(this.postList);
					}
						
				})
			},
			/**
			 * 删除帖子
			 * @param {Object} commentId
			 */
			handleDeletePost(postId) {
				const data = [];
				data.push(parseInt(postId));
				deleteMyPost(data).then((res) => {
					if(responseStatus(res)){
						this.$message.success("删除成功");
						this.handleGetPostList();
					}				   							   
				})
			},
			
			/* 加精 */
			handleAddEssences(postId,type) {
				const data = {
					postId: postId,
					type: type
				}
				addEssences(data).then((res) => {
					if(responseStatus(res)){
						this.$message.success("操作成功");
						this.handleGetPostList();
					}				   							   
				})
			},
			
			/* 模糊搜索帖子 */
			handleSearchPost(){
				const data = {
					content: this.keyword,
					pageNum: this.queryInfo.pageNum,
					pageSize: this.queryInfo.pageSize
				}
				getSearchList(data).then(res => {
					if(responseStatus(res)){
						this.postList = res.data.data.list;
						this.queryInfo.pageNum = res.data.data.pageNum;
						this.queryInfo.pageSize = res.data.data.pageSize;
						this.queryInfo.total = res.data.data.total;
					}
				})
				
			},
			
			/* 分页处理 */
			handleCurrentChange(newPage){
				this.queryInfo.pageNum = newPage;
				this.handleGetPostList();
			},
			
			//跳转到轮播展示界面
			routeToBannerPage(postId){
				this.$router.push({path: '/addBanner',query: {postId: postId}});
			}
		},
		watch: {
			keyword: function (){
				this.queryInfo.pageNum = 1;
				this.queryInfo.pageSize = 10;
				this.queryInfo.total = 0;
				this.handleSearchPost();	
			}
		},
		created() {
			this.handleGetPostList();
		}
	}
</script>

<style lang="less" scoped>
	.admin-nav {
		margin: 0 20px;
	}
	.post-list-card {
		margin: 20px;
		padding-top: 20px;
		border-radius: 5px;
		.search-item {
			max-width: 500px;
			margin-bottom: 10px;
			
			span {
				cursor: pointer;
			}
		}
	}
	.pagination {
		margin-top: 20px;
		text-align: right;
	}
	
</style>
