<template>
	<div class="wrap">
		
		<user-info-component v-if="user.userId!=null" :userId="user.userId" :isHost="isHost" style="margin: 40px 0px;"></user-info-component> 
		
		<el-row :gutter="20">
			<!-- 导航菜单 -->
			<el-col :xs="24" :sm="7" :md="6" :lg="6" :xl="5">
				<!-- 菜单导航 -->
				<el-menu :default-active="'1'" @select="handleSelect">
					<el-menu-item index="1">
						<i class="el-icon-user-solid"></i>
						<span>关注的人</span>
					</el-menu-item>
					<el-menu-item index="2">
						<i class="el-icon-s-opportunity"></i>
						<span>粉丝列表</span>
					</el-menu-item>
					<el-menu-item index="3">
						<i class="el-icon-s-comment"></i>
						<span>评论列表</span>
					</el-menu-item>
				</el-menu>
			</el-col>
			<!-- 菜单内容 -->
			<el-col :xs="24" :sm="17" :md="18" :lg="18" :xl="19">
				<div class="menu-content">
					<!-- 关注的人 -->
					<div v-if="selectIndex == 1">
						<follow-list-component v-if="user.userId!=null" :userId="user.userId" :isHost="isHost"></follow-list-component>
					</div>
					
					<!-- 粉丝列表 -->
					<div v-if="selectIndex == 2">
						<fans-list-component v-if="user.userId!=null" :userId="user.userId" :isHost="isHost"></fans-list-component>
					</div>
					
					<!-- 评论 -->
					<div v-if="selectIndex == 3">
						<comment-list-component v-if="user.userId!=null" :userId="user.userId" :isHost="isHost"></comment-list-component>
					</div>	
				</div>
			</el-col>
		</el-row>
	</div>
</template>

<script>
	import UserInfoComponent from './user_center/component/UserInfoComponent.vue'
	import FollowListComponent from './user_center/component/FollowListComponent.vue'
	import FansListComponent from './user_center/component/FansListComponent.vue'
	import commentListComponent from './user_center/component/myCommentComponent.vue'
	import { getUser,getFollowNum,getFans,getPostListByUserId } from '@/api/user'
	import { responseStatus } from '@/utils/responseStatusUtil'
	export default{
		name: 'userCenter',
		data(){
			return{
				//用户信息
				localUser: null,
				user: {},
				//选中的菜单项
				selectIndex: 1,
			}
		},
		computed: {
			//判断是否为自己
			isHost: function(){
				if(this.localUser != null){
					if(this.localUser.userId == this.user.userId) return true;
				}
				return false;
			},
		},
		methods:{
			/* 获取用户信息 */
			handleGetUser(){
				const data = {
					userId: this.$route.query.userId
				}
				getUser(data).then(res => {
					if(responseStatus(res))
						this.user = res.data.data;
						
						
				})
			},
			
			/**
			 * 获得关注数
			 */
			handleGetFollowNum(){
				getFollowNum().then(res => {
					if(responseStatus(res))
						this.FollowNum = res.data.data; 
				})  	
			},
			
			/**
			 * 获取粉丝数
			 */
			handleGetFans(){
				getFans().then(res => {
					if(responseStatus(res))
						this.fans = res.data.data; 
				})
			},
			
			/* 菜单选择器 */
			handleSelect(index){
				this.selectIndex = index;
			}
		},
		components:{
			UserInfoComponent,
			FollowListComponent,
			FansListComponent,
			commentListComponent,
			
		},
		created() {
			this.localUser = JSON.parse(window.sessionStorage.getItem("user")); 
			this.handleGetUser();
		}
	}
</script>

<style lang="less" scoped>
	/* 导航菜单样式 */
	.el-menu {
		border: none !important;
	}
	
	/* 导航菜单右侧内容 */
	.menu-content {
		border-radius: 10px;
		background-color: #FFF;
	}
	
	
</style>
