    export const getNextExp = (exp)=>{
        let level = getUserLevel(exp);
        switch (level) {
            case 1:
                return 5-exp;
            case 2:
                return 15-exp;
            case 3:
                return 30-exp;
            case 4:
                return 50-exp;
            case 5:
                return 100-exp;
            case 6:
                return 200-exp;
            case 7:
                return 500-exp;
            case 8:
                return 1000-exp;
            case 9:
                return 2000-exp;
            case 10:
                return 3000-exp;
            case 11:
                return 6000-exp;
            case 12:
                return 10000-exp;
            case 13:
                return 18000-exp;
            case 14:
                return 30000-exp;
            case 15:
                return 60000-exp;
            case 16:
                return 100000-exp;
            case 17:
                return 300000-exp;
            case 18:
                return 0;
        }
        return 5;
    }
