<template>
	<div>
		<!-- 头部导航 -->
		<el-breadcrumb separator="/" class="admin-nav">
		  <el-breadcrumb-item :to="{ path: '/adminHome' }">首页</el-breadcrumb-item>
		  <el-breadcrumb-item>分类管理</el-breadcrumb-item>
		  <el-breadcrumb-item>分类列表</el-breadcrumb-item>
		</el-breadcrumb>
		
		<!-- 卡片视图区域 -->
		<el-card class="category-list-card">
			<div slot="header">
			    <el-button  type="primary" @click="addDialogVisible=true">添加分类</el-button>
			</div>
			<div>
				<!-- 分类列表 -->
				<el-table :data="categoryList" style="width: 100%">
					<el-table-column prop="categoryId" label="分类ID"/>
					<el-table-column prop="categoryImage" label="分类图片">
						<template slot-scope="scope">
						　　　　<img :src="scope.row.categoryImage" width="50" height="50"/>
						</template>
					</el-table-column>
					<el-table-column prop="categoryName" label="分类名字"/>
					<el-table-column label="推荐" prop="isRecommend" >
						<template slot-scope="scope">
								<el-tag dark type="success" v-if="scope.row.isRecommend == 0">普通</el-tag>
						　　　　	<el-tag dark type="warning" v-if="scope.row.isRecommend == 1">推荐</el-tag>
						</template>
					</el-table-column>
					<el-table-column label="操作">
						<template slot-scope="scope">
							<el-tooltip effect="dark" content="修改" placement="top" :enterable="false">
								<el-button type="primary" size="mini" icon="el-icon-edit" @click="showUpdateDialog(scope.row)"></el-button>
							</el-tooltip>
							<el-tooltip effect="dark" content="删除" placement="top" :enterable="false">
								<el-button type="danger" size="mini" icon="el-icon-delete" @click="handleDeleteCategory(scope.row.categoryId)"></el-button>
							</el-tooltip>
							<el-tooltip v-if="!scope.row.isRecommend" effect="dark" content="推荐" placement="top" :enterable="false">
								<el-button type="primary" size="mini" icon="el-icon-plus" @click="handleRecommend(scope.row.categoryId,1)"></el-button>
							</el-tooltip>
							<el-tooltip v-else effect="dark" content="取消推荐" placement="top" :enterable="false">
								<el-button size="mini" icon="el-icon-close" @click="handleRecommend(scope.row.categoryId,0)"></el-button>
							</el-tooltip>
						</template>
					</el-table-column>
				</el-table>
			</div>
		</el-card>
		
		<!-- 添加分类对话框 -->
		<el-dialog title="添加分类" :visible.sync="addDialogVisible" width="60%" @close="addDialogClosed">
			<!-- 添加分类表单 -->
			<el-form ref="addFormRef" :model="addForm" :rules="addFormRules" label-width="80px">
					<el-form-item label="分类名称" prop="categoryName">
						<el-input v-model="addForm.categoryName"></el-input>
					</el-form-item>
					<el-form-item>
						<el-upload action="*" ref="upload" list-type="picture-card"
							:auto-upload="false"
							:http-request="handleAddCategory"
							:limit="1">
							<img v-if="imageUrl" :src="imageUrl">
							<i v-else class="el-icon-plus avatar-uploader-icon"></i>
						</el-upload>
					</el-form-item>
				</el-form>
			<!-- 底部按钮 -->
			<span slot="footer">
				<el-button @click="addDialogVisible=false">取 消</el-button>
				<el-button type="primary" @click="upload">增 加</el-button>
			</span>
		</el-dialog>
		
		<!-- 修改分类对话框 -->
		<el-dialog title="修改分类" :visible.sync="updateDialogVisible" width="60%" @close="updateDialogClosed">
			<!-- 修改分类表单 -->
			<el-form ref="updateFormRef" :model="updateForm" :rules="updateFormRules" label-width="80px">
					<el-form-item label="分类ID" prop="categoryId">
						<el-input v-model="updateForm.categoryId" disabled></el-input>
					</el-form-item>
					<el-form-item label="分类图片" prop="categoryImage">
						<img :src="updateForm.categoryImage" width="150"></img>
						<el-upload action list-type="picture" :http-request="handleUploadImage">
							<el-button size="small" type="primary">点击修改</el-button>
						</el-upload>
					</el-form-item>
					<el-form-item label="分类名称" prop="categoryName">
						<el-input v-model="updateForm.categoryName"></el-input>
						<el-button type="primary" @click="handleUpdateCategoryName">点击修改</el-button>
					</el-form-item>
				</el-form>
			<!-- 底部按钮 -->
			<span slot="footer">
				<el-button @click="updateDialogVisible=false">取 消</el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	import { addCategory,getCategory,deleteCategory,recommendCategory,uploadCategoryImg,updateCategoryName } from '@/api/admin'
	import { responseStatus } from '@/utils/responseStatusUtil'
	export default {
		name: 'categoryList',
		inject: ['reload'],
		data(){
			return {
				imageUrl: '',
				//列表信息
				queryInfo:{
					//总页数
					pageTotal: 0
				},
				//添加分类对话框显示&隐藏
				addDialogVisible: false,
				//修改分类对话框显示&隐藏
				updateDialogVisible: false,
				//添加分类的表单数据
				addForm: {
					categoryName: ''
				},
				//修改分类的表单数据
				updateForm: {
					categoryId: '',
					categoryImage: '',
					categoryName: ''
				},
				//添加表单的验证规则
				addFormRules: {
					categoryName: [
						{ required:true, message:'分类名称不能为空', trigger:'blur'}
					]
				},
				//修改表单的验证规则
				updateFormRules: {
					categoryId: [
						{ required:true, message:'分类ID不能为空', trigger:'blur'}
					],
					categoryName: [
						{ required:true, message:'分类名称不能为空', trigger:'blur'}
					]
				},
				//分类列表信息
				categoryList: [],
			}
		},
		methods:{
			
			/* 获得分类列表 */
			handleGetCategory(){
				getCategory().then(res => {
					if(responseStatus(res)){
						this.categoryList = res.data.data;
					}
				})
			},
			
			//上传分类图片
			handleUploadImage(file){
				const formData = new FormData();
					formData.append('categoryId',this.updateForm.categoryId);
					formData.append('categoryImage', file.file);
					uploadCategoryImg(formData).then(res => {
						if(responseStatus(res)){
							this.$message.success("更新成功!!!");
							this.updateDialogVisible = false;
							this.handleGetCategory();
							this.reload();
						}
					})
			},
			
			
			handleUpdateCategoryName(){
				const data = {
					categoryId: this.updateForm.categoryId,
					categoryName: this.updateForm.categoryName
				}
				updateCategoryName(data).then(res => {
					if(responseStatus(res)){
						this.$message.success("更新成功！");
						this.updateDialogVisible = false;
						this.handleGetCategory();
						this.reload();
					}
				})
			},
			
			//监听添加分类对话框关闭事件
			addDialogClosed(){
				this.$refs.addFormRef.resetFields(); 
			},
			//监听修改分类对话框开启事件
			showUpdateDialog(category){
				this.updateForm.categoryId = category.categoryId,
				this.updateForm.categoryImage = category.categoryImage,
				this.updateForm.categoryName = category.categoryName,
				this.updateDialogVisible = true;
				
			},
			//监听修改分类对话框关闭事件
			updateDialogClosed(){
				this.$refs.updateFormRef.resetFields(); //清空输入框
			},
			/* 添加分类 */
			handleAddCategory(file){
				this.$refs.addFormRef.validate(valid => {
					const categoryFormData = new FormData();
					categoryFormData.append('categoryName',this.addForm.categoryName);
					categoryFormData.append('file', file.file);
					if(valid){
						addCategory(categoryFormData).then(res => {
							if(responseStatus(res)){
								this.$message.success("创建成功!!!");
								this.addDialogVisible = false;
								this.handleGetCategory();
							}
						})
					}
				})
			},
			upload(){
				this.$refs.upload.submit();
			},
			
			/* 删除分类 */
			handleDeleteCategory(categoryId){
				const data = {
					categoryId: categoryId
				}
				deleteCategory(data).then(res => {
					if(responseStatus(res)){
						this.$message.success("删除成功！");
						this.handleGetCategory();
					}
				})
				
			},
			//修改分类
			updateCategory(){
				this.$refs.updateFormRef.validate(valid =>{
					if(valid){
						this.$axios.post('admin/updateCategory',this.$qs.stringify({ 
							categoryId: this.updateForm.categoryId,
							categoryImage: this.updateForm.categoryImage,
							categoryName: this.updateForm.categoryName}))
								   .then(res => {
									   //console.log(res);
									   if(res.status !== 200)
											this.$message.error("修改分类失败!!!");
										else if(res.data.status !== 10000)
											this.$message.error(res.data.msg+"!!!");
										else
											this.$message.success("修改成功!!!");
								   })
					}else return;
				})
			},
			
			/* 推荐分类 */
			handleRecommend(categoryId,type){
				const data = {
					categoryId: categoryId,
					type: type
				}
				recommendCategory(data).then(res => {
					if(responseStatus(res)){
						this.$message.success("推荐成功！");
						this.updateDialogVisible = false;
					}
				})
			}
			
		},
		created() {
			this.handleGetCategory();
		}
	}
</script>

<style scoped="scoped">
	.admin-nav {
		margin: 0 20px;
	}
	.category-list-card {
		margin: 20px;
		padding-top: 20px;
		border-radius: 5px;
	}
	.pagination {
		margin-top: 20px;
		text-align: right;
	}
</style>
