<template>
	<div class="re-container">
		<ul>
			<li class="re-item" v-for="item in recommendPostList" @click="routeToDetailPage(item.postId)">
				<!-- 头部信息 -->
				<header class="re-header">
					<div class="user-info">
						<a @click.stop="routeToOthersPage(item.userId)">
							<el-avatar :size="30" :src="item.headSculpture" ></el-avatar>
						</a>
						<span class="username">{{item.userName}}</span>
						<el-tag v-if="item.userInfo != null" class="level" size="mini" >
							<span>LV{{handleGetUserLevel(item.userInfo.exp)}}</span>
							<span class="icon-margin-left">{{handleGetLevelName(item.userInfo.exp)}}</span>
						</el-tag>
						<span class="create_time">{{item.createTime}}</span>
					</div>
				</header>
				<!-- 内容信息 -->
				<section class="re-main">
					<div class="title">
						<el-tag class="category" size="small" >{{item.categoryName}}</el-tag>
						<h3>{{item.title}}</h3>
					</div>
					<div class="article">
						<div class="content">{{item.content}}</div>
						<el-image class="image" v-for="image in item.postImages" :src="image"></el-image>
					</div>
				</section>
				<!-- 底部信息 -->
				<footer class="re-footer">
					<div class="bottom-info">
						<div class="update_time">最后更新：{{item.updateTime}}</div>
						<div class="post-info">
							<span><i class="el-icon-view icon-margin"></i>{{item.viewNum}}</span>
							<span><i class="el-icon-chat-dot-round icon-margin"></i>{{item.commentNum}}</span>
							<span><i class="el-icon-circle-check icon-margin"></i>{{item.goodNum}}</span>
						</div>
					</div>
				</footer>
			</li>
			<li class="loading" v-if="status === 0">
				<span>正在加载...</span>
			</li>
			<li class="loading" v-if="more === 0">
				<span>没有更多了</span>
			</li>
		</ul>
	</div>
</template>

<script>
	import { getPostList,getPostImage,getUser } from '@/api/user'
	import { responseStatus } from '@/utils/responseStatusUtil'
	import { getScrollTop, getClientHeight, getScrollHeight } from '@/utils/scrollUtils'
	import { getUserLevel, getLevelNameByExp } from '@/utils/ExpUtils'
	export default {
		data(){
			return {
				status: 1,	//懒加载是否完成（1 完成  0 未完成）
				more: 1,	//懒加载是否更多（1 有  0 没有）
				recommendPage: {
					pageNum: 1,
					pageSize: 10,
					isLastPage: false,
				},
				recommendPostList: []
			}
		},
		methods: {
			/* 获取推荐帖子 */
			handleGetRecommendPostList(){
				const data = {
					orderBy: 4,
					pageNum: this.recommendPage.pageNum,
					pageSize: this.recommendPage.pageSize
				}
				getPostList(data).then(res => {
					if(responseStatus(res)){
						let result = res.data.data.list;
						for(let index in result){
							//获取每个帖子的图片
							const data = { postId: result[index].postId };
							getPostImage(data).then(res => {
								if(responseStatus(res))
									this.$set(result[index],'postImages',res.data.data);
							});
							//获取用户的经验
							const userId = { userId: result[index].userId };
							getUser(userId).then(res => {
								if(responseStatus(res))
									this.$set(result[index],'userInfo',res.data.data);
							});
						}
						this.recommendPage.pageNum = res.data.data.pageNum;
						this.recommendPage.pageSize = res.data.data.pageSize;
						this.recommendPage.isLastPage = res.data.data.isLastPage;
						this.recommendPostList = this.recommendPostList.concat(result);
						this.status = 1;
					}
				})
			},
			
			/* 跳转到帖子详情 */
			routeToDetailPage(postId){
				this.$router.push({path: '/postDetailPage',query: {postId: postId}});
			},
			/* 跳转到其他用户的信息界面 */
			routeToOthersPage(userId){
				this.$router.push({path: '/userCenter',query: {userId: userId}});
			},
			
			/**
			 * 页面下拉懒加载
			 */
			onScroll () {
				if (getScrollHeight() - getClientHeight() - getScrollTop() <= 0) {
					//判断加载是否完成
					if (this.status === 1 && this.more == 1) {
						this.status = 0;
						this.recommendPage.pageNum += 1;
						this.handleGetRecommendPostList();
					}
					if(this.recommendPage.isLastPage){
						this.more = 0;
					}
					
				}
			},
			
			/**
			 * 用户经验等级
			 */
			
			
			/* 判断用户经验 */
			handleGetUserLevel(exp) {
				return getUserLevel(exp);
			},
			
			/* 判断用户等级 */
			handleGetLevelName(exp) {
				return getLevelNameByExp(exp);
			}
		},
		mounted() {
			this.handleGetRecommendPostList();
			this.$nextTick(function () {
				window.addEventListener('scroll', this.onScroll)
			});
		}
	}
</script>

<style lang="less" scoped>
	.re-container {
		background-color: #FFF;
		.re-item {
			padding: 20px 30px;
			border-bottom: 1px #EBEBEB solid;
			/* 头部信息 */
			.re-header {
				/* 用户信息 */
				.user-info {
					display: flex;
					flex-wrap: wrap;
					align-items: center;
					
					.username {
						color: #555;
						font-size: 14px;
						margin: 0px 10px;
					}
					.username:hover {
						cursor: pointer;
						color: #00C3FF;
					}
					.level {
						color: #FFF;
						border: none;
						margin-right: 10px;
						background-color: #FFBF00;
						
					}
					.create_time {
						color: #CCC;
						font-size: 12px;
					}
				}
			}
			
			/* 内容信息 */
			.re-main {
				padding: 15px 0px;
				/*标题部分*/
				.title {
					display: flex;
					flex-wrap: wrap;
					align-items: center;
					.category {
						color: #FFF;
						font-weight: 600;
						margin-right: 5px;
						background-color: #00C3FF;
					}
					h3 {
						font-size: 16px;
						font-weight: 600;
						color: #333;
					}
				}
				
				/*内容部分*/
				.article{
					.content {
						font-size: 14px;
						color: #999;
						white-space: nowrap;
						overflow: hidden;
						text-overflow: ellipsis;
					}
					.image {
						width: 120px;
						margin: 10px;
						margin-left: 0px;
						border-radius: 10px;
					}
				}
			}
			.re-footer {
				.bottom-info {
					display: flex;
					flex-wrap: wrap;
					align-items: center;
					justify-content: space-between;
					.update_time {
						color: #CCC;
						font-size: 12px;
					}
				}
				.post-info {
					color: #CCC;
					font-size: 16px;
					display: flex;
					align-items: center;
					span {
						display: block;
						margin-right: 20px;
					}
				}
				
			}
			.re-main:hover {
				cursor: pointer;
				h3 {
					color: #00C3FF;
				}
			}
		}
		
		//页面加载样式
		.loading {
			color: #808080;
			font-size: 14px;
			padding: 20px 30px;
			text-align: center;
		}
		
	}
</style>
