<template>
	<div>
		<!-- 导航标签 -->
		<el-breadcrumb separator="/" class="admin-nav">
			<el-breadcrumb-item :to="{ path: '/adminUserList' }">首页</el-breadcrumb-item>
			<el-breadcrumb-item>用户管理</el-breadcrumb-item>
			<el-breadcrumb-item>用户列表</el-breadcrumb-item>
		</el-breadcrumb>
		
		<el-card class="user-list">
			<div class="search-item">
				<el-input placeholder="请输入内容" v-model="keyword">
					<template slot="append">
						<span>搜索</span>
					</template>
				</el-input>
			</div>
			<el-table :data="userList" style="width: 100%">
				<el-table-column prop="userId" label="用户ID"/>
				<el-table-column prop="username" label="用户名"/>
				<el-table-column prop="role" label="角色">
					<template slot-scope="scope">
							<el-tag dark type="success" v-if="scope.row.role == 1">普通</el-tag>
					　　　　	<el-tag dark type="danger" v-if="scope.row.role == 2">管理员</el-tag>
					</template>
				</el-table-column>
				<el-table-column prop="createTime" label="创建时间"/>
				<el-table-column prop="categoryName" label="状态">
					<template slot-scope="scope">
							<el-tag dark type="success" v-if="!scope.row.isBan">正常</el-tag>
					　　　　	<el-tag dark type="danger" v-else>封禁</el-tag>
					</template>
				</el-table-column>
			</el-table>
		</el-card>
		<el-pagination class="pagination" :current-page.sync="userListPage.pageNum" :page-size="userListPage.pageSize" :total="userListPage.total"
				layout="total, prev, pager, next, jumper" @current-change="handleCurrentChange" >
		</el-pagination>
	</div>
	
</template>

<script>
	import { getUserList } from '@/api/admin'
	import { responseStatus } from '@/utils/responseStatusUtil'
	export default {
		data(){
			return {
				userListPage: {
					pageNum: 1,
					pageSize: 10,
					total: 0
				},
				userList: [],
				keyword: ''
			}
		},
		methods: {
			/* 获取用户列表 */
			HandleGetUserList(){
				const data = {
					content: '%',
					pageNum: this.userListPage.pageNum,
					pageSize: this.userListPage.pageSize
				}
				getUserList(data).then(res => {
					if(responseStatus(res)){
						this.userList = res.data.data.list;
						this.userListPage.pageNum = res.data.data.pageNum; 
						this.userListPage.pageSize = res.data.data.pageSize; 
						this.userListPage.total = res.data.data.total; 
					}
				})
			},
			
			handleSearchUser(){
				const data = {
					content: this.keyword,
					pageNum: this.userListPage.pageNum,
					pageSize: this.userListPage.pageSize
				}
				getUserList(data).then(res => {
					if(responseStatus(res)){
						this.userList = res.data.data.list;
						this.userListPage.pageNum = res.data.data.pageNum; 
						this.userListPage.pageSize = res.data.data.pageSize; 
						this.userListPage.total = res.data.data.total; 
					}
				})
			},
			
			/* 当前页数改变 */
			handleCurrentChange(newPage){
				this.userListPage.pageNum = newPage;
				this.HandleGetUserList();
			},
		},
		watch: {
			keyword: function (){
				this.userListPage.pageNum = 1;
				this.userListPage.pageSize = 10;
				this.userListPage.total = 0;
				if(this.keyword != '' && this.keyword != '%'){
					this.handleSearchUser();
				}else{
					this.HandleGetUserList();
				}
				
			}
		},
		created() {
			this.HandleGetUserList();
		}
	}
</script>

<style lang="less" scoped>
	.admin-nav {
		margin: 0 20px;
	}
	.user-list {
		margin: 20px;
		border-radius: 10px;
		padding-top: 20px;
		
		.search-item {
			max-width: 500px;
			margin-bottom: 10px;
			
			span {
				cursor: pointer;
			}
		}
	}
	.pagination {
		text-align: right;
	}
</style>
