<template>
	<!-- 个人信息 -->
	<div class="userInfo-container">
		<!-- 用户头部信息 -->
		<div class="userInfo-header">
			<!-- 头像 -->
			<div class="avatar">
				<el-avatar :size="80" :src="user.headSculpture" @click.native="isHost?updateAvatarDialogVisible=true : updateAvatarDialogVisible=false"></el-avatar>
			</div>
			<div class="user-info-msg">
				<span>{{user.username}}</span>
				<div>
					<span v-if="user.sex == 0">性别：保密<i class="iconfont icon-suo color-suo" style="font-size: 20px"></i></span>
					<span v-if="user.sex == 1">性别：男<i class="iconfont icon-nan color-nan" style="font-size: 20px"></i></span>
					<span v-if="user.sex == 2">性别：女<i class="iconfont icon-nv color-nv" style="font-size: 20px"></i></span>
				</div>
				<span>
					个性签名: {{user.personalizedSignature}}
					<i v-if="isHost" class="el-icon-edit" @click="updateDialogVisible=true" style="cursor: pointer;"></i>
				</span>
				<span v-if="isHost">
					<el-button type="text" class="hidden-sm-and-up" style="padding: 0;" @click="updateIntervalDiaglogVisible = true">编辑资料</el-button>
				</span>
			</div>
			<div class="user-info-btn hidden-xs-only" v-if="isHost">
				<el-button size="small" @click="updateIntervalDiaglogVisible = true">编辑资料</el-button>
			</div>
		</div>
		<!-- 用户详细信息 -->
		<div class="user-info-detail">
			<p class="title">详细信息</p>
			<div class="user-info-detail-box">
				<div>
					<span><i class="el-icon-map-location icon-margin"></i>学校：{{user.school != null?user.school:"未填写"}}</span>
					<span><i class="el-icon-chat-round icon-margin"></i>QQ号：{{user.qq != null?user.qq:"未填写"}}</span>
					<span><i class="el-icon-eleme icon-margin"></i>个人网站：{{user.personalWebsite != null?user.personalWebsite:"未填写"}}</span>
					<span><i class="el-icon-chat-dot-round icon-margin"></i>微信号：{{user.wechat != null?user.wechat:"未填写"}}</span>
					<!-- xs屏幕下 -->
					<div class="hidden-sm-and-up">
						<span><i class="el-icon-message icon-margin"></i>电子邮件：{{user.email != null?user.email:"未填写"}}</span>
						<span><i class="el-icon-edit icon-margin"></i>字数：{{user.wordNumber != null?user.wordNumber:"未填写"}}</span>
						<span><i class="el-icon-edit icon-margin"></i>粉丝数：{{fansNum}}</span>
						<span><i class="el-icon-star-off icon-margin"></i>关注数：{{followNum}}</span>
					</div>
				</div>
				<div class="hidden-xs-only">
					<span><i class="el-icon-message icon-margin"></i>电子邮件：{{user.email != null?user.email:"未填写"}}</span>
					<span><i class="el-icon-edit icon-margin"></i>字数：{{user.wordNumber != null?user.wordNumber:"未填写"}}</span>
					<span><i class="el-icon-edit icon-margin"></i>粉丝数：{{fansNum}}</span>
					<span><i class="el-icon-star-off icon-margin"></i>关注数：{{followNum}}</span>
				</div>
			</div>
		</div>
		
		<!-- 修改签名对话框 -->
		<el-dialog title="修改简介" :visible.sync="updateDialogVisible" width="40%" @close="updateDialogClosed">
			<el-form ref="updateFormRef" :model="updateForm" :rules="updateFormRules" label-width="80px">
					<el-form-item label="简介" prop="sign">
						<el-input v-model="updateForm.sign" maxlength="100"></el-input>
					</el-form-item>
				</el-form>
			<span slot="footer">
				<el-button @click="updateDialogVisible=false">取 消</el-button>
				<el-button type="primary" @click="updateSign">修 改</el-button>
			</span>
		</el-dialog>
		
		
		<!-- 编辑个人资料对话框 -->
		<el-dialog title="编辑资料" :visible.sync="updateIntervalDiaglogVisible" width="60%" @close="updateIntervalDialogClosed()">
			<el-form ref="updateIntervalFormRef" :model="updateIntervalForm" :rules="updateIntervalFormRules" label-width="80px">
					<el-form-item label="个人网站: " prop="personalWebsite">
						<el-input v-model="updateIntervalForm.personalWebsite" maxlength="100"  placeholder="请输入个人网站"></el-input>
					</el-form-item>
					<el-form-item label="QQ号: " prop="qq">
						<el-input v-model="updateIntervalForm.qq" maxlength="20" placeholder="请输入QQ号"></el-input>
					</el-form-item>
					<el-form-item label="微信号: " prop="wechat">
						<el-input v-model="updateIntervalForm.wechat" maxlength="20" placeholder="请输入微信号"></el-input>
					</el-form-item>
					<el-form-item label="性别: " prop="sex">
						<el-select v-model="updateIntervalForm.sex" placeholder="请选择性别">
							<el-option label="保密" value="0"></el-option>
							<el-option label="男" value="1"></el-option>
							<el-option label="女" value="2"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="学校: " prop="school">
						<el-input v-model="updateIntervalForm.school" maxlength="100" placeholder="请输入学校名称"></el-input>
					</el-form-item>
				</el-form>
			<span slot="footer">
				<el-button @click="updateIntervalDiaglogVisible=false">取 消</el-button>
				<el-button type="primary" @click="handleEditInterval">修 改</el-button>
			</span>
		</el-dialog>
		
		<!-- 修改头像对话框 -->
		<el-dialog title="修改头像" :visible.sync="updateAvatarDialogVisible" width="60%">
			<el-upload action="*" ref="upload" list-type="picture-card"
				:auto-upload="false"
				:http-request="handleUpdateAvatar"
				:limit="1">
				<img v-if="imageUrl" :src="imageUrl">
				<i v-else class="el-icon-plus avatar-uploader-icon"></i>
			</el-upload>
			<span slot="footer">
				<el-button @click="updateAvatarDialogVisible=false">取 消</el-button>
				<el-button type="primary" @click="upload">修 改</el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	import { getUser, getFollowNum, getFansNum, updateInterval,updateAvatar } from '@/api/user'
	import { responseStatus } from '@/utils/responseStatusUtil'
	export default {
		props: {
			userId: { type: Number, requires: false },
			isHost: { type: Boolean, requires: false }
		},
		data(){
			return {
				user: {},
				//修改头像的头像地址
				imageUrl: '',
				updateIntervalForm: {
					personalWebsite: null,
					qq: null,
					wechat: null,
					sex: null,
					school: null
				},
				//修改简介的表单数据
				updateForm: {
					sign: ''
				},
				fansNum: 0,
				followNum: 0,
				//修改信息对话框显示&隐藏
				updateDialogVisible: false,
				//编辑个人资料对话框显示&隐藏
				updateIntervalDiaglogVisible: false,
				//修改头像对话框显示&隐藏
				updateAvatarDialogVisible: false,
				
				//编辑资料的验证规则
				updateIntervalFormRules: {
					sex: [
						{ required:true, message:'请选择性别', trigger:'blur'}
					]
				},
				//修改简介表单的验证规则
				updateFormRules: {
					sign: [
						{ required:true, message:'请输入内容', trigger:'blur'}
					],
				},
			}
		},
		methods: {
			/* 获取用户信息 */
			handleGetUser(userId){
				if(this.userId == null && this.isHost == false)
					return 
				const data = {
					userId: userId
				}
				getUser(data).then(res => {
					if(responseStatus(res)){
						this.user = res.data.data;
					}
				})
			},
			
			/* 获得关注数 */
			handleGetFollowNum(userId){
				const data = {
					userId: userId
				}
				getFollowNum(data).then(res => {
					if(responseStatus(res))
						this.followNum = res.data.data; 
				})  	
			},
			
			/* 获取粉丝数 */
			handleGetFansNum(userId){
				const data = {
					userId: userId
				}
				getFansNum(data).then(res => {
					if(responseStatus(res))
						this.fansNum = res.data.data; 
				})
			},
			
			/* 修改简介 */
			updateSign(){
				this.$axios.post('/user/update',this.$qs.stringify({
					personalizedSignature: this.updateForm.sign }))
						   .then(res => {
								if(res.status !== 200)
								   	return this.$message.error("修改签名失败!!!");
								else if(res.data.status !== 10000)
								   	this.$message.error(res.data.msg+"!!!");
								else{
									this.updateDialogVisible = false;
									this.$message.success("修改成功!!!");
									this.handleGetUser(this.userId);
								}		   	
							})
			},
			
			/* 编辑个人资料 */
			handleEditInterval() {
				this.$refs.updateIntervalFormRef.validate(valid => {
					if(valid){
						updateInterval(this.updateIntervalForm).then(res => {
							if(responseStatus(res)){
								this.$message.success("修改成功！");
								this.updateIntervalDiaglogVisible = false;
								this.handleGetUser(this.userId);
							}
						})
					}	
				})	
			},
			
			//上传头像
			handleUpdateAvatar(file){
				const formData = new FormData();
				formData.append('file', file.file);
				updateAvatar(formData).then(res => {
					if(responseStatus(res)){
				    	this.updateAvatarDialogVisible = false;
				    	this.$message.success("修改成功!!!");
						this.handleGetUser();
						this.$bus.$emit('change');
					}		   	
				})
			},
			upload(){
				this.$refs.upload.submit();
			},
			
			/* 对话框关闭 */
			//编辑资料对话框
			updateIntervalDialogClosed(formRef){
				this.$refs.updateIntervalFormRef.resetFields(); //清空输入框
			},
			//监听修改信息对话框关闭事件
			updateDialogClosed(){
				this.$refs.updateFormRef.resetFields(); //清空输入框
			},
		},
		mounted() {
			this.handleGetUser(this.userId);
			this.handleGetFansNum(this.userId);
			this.handleGetFollowNum(this.userId);
		}
	}
</script>

