				span {
					display: block;
					margin-top: 5px;
				}
				>span:nth-child(1) {
					font-size: 18px;
					font-weight: 700;
					margin-top: 0px;
				}
			}
			.user-info-btn {
				float: right;
			}
		}
		//个人详细信息
