<template>
	<div>
		<!-- 导航标签 -->
		<el-breadcrumb separator="/" class="nav-bread">
			<el-breadcrumb-item :to="{ path: '/adminUserList' }">首页</el-breadcrumb-item>
			<el-breadcrumb-item>用户管理</el-breadcrumb-item>
			<el-breadcrumb-item>封禁用户</el-breadcrumb-item>
		</el-breadcrumb>
		<el-card id="ban-user">
			<!-- 用户列表 -->
			<el-form ref="banUserRef" :model="banUserForm" :rules="banUserRules" label-width="100px">
				<el-form-item label="封禁用户:" prop="username">
					<el-input type="text" placeholder="请输入要封禁的用户名" v-model="banUserForm.username" prefix-icon="el-icon-user"/>
				</el-form-item>
				<el-form-item label="封禁时间:" prop="banTime">
					<el-date-picker type="datetime" placeholder="选择封禁时间" v-model="banUserForm.banTime" :picker-options="pickerOptions"/>
				</el-form-item>
				<el-form-item label="封禁原因:" prop="banMsg">
					<el-input type="textarea" placeholder="请输入封禁原因" v-model="banUserForm.banMsg" prefix-icon="el-icon-chat-round" :rows="5"/>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="banUserAction('banUserRef')">提交</el-button>
					<el-button @click="resetForm('banUserRef')">重置</el-button>
					<el-button type="primary" @click="unBanDialogVisible = true" style="float: right;">解封用户</el-button>
				</el-form-item>
			</el-form>
		</el-card>
		
		<!-- 解封对话框 -->
		<el-dialog title="解封用户" :visible.sync="unBanDialogVisible" width="40%" @closed="resetForm('unBanUserRef')">
			<el-form ref="unBanUserRef" :model="unBanUserForm" :rules="unBanUserRules" label-width="80px">
					<el-form-item label="解封用户" prop="username">
						<el-input type="text" placeholder="请输入要解封的用户名" v-model="unBanUserForm.username" prefix-icon="el-icon-user" ></el-input>
					</el-form-item>
				</el-form>
			<span slot="footer">
				<el-button @click="unBanDialogVisible=false">取 消</el-button>
				<el-button type="primary" @click="unBanUserAction('unBanUserRef')">解 封</el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	import { banUserByName, unBanUserByName } from '../../api/admin'
	import { responseStatus } from '../../utils/responseStatusUtil'
	export default {
		name: 'banUser',
		data(){
			var validateBanTime = (rule, value, callback) => {
			        if (value == '') {
			          callback(new Error('请选择封禁时间'));
			        } else {
			          if (value.getTime() < Date.now()) {
			           callback(new Error('请输入真确的封禁时间'));
			          }
			          callback();
			        }
			      };
			return {
				//封禁用户表单
				banUserForm: {
					username: '',
					banTime: 0,
					banMsg: ''
				},
				//解封用户表单
				unBanUserForm: {
					username: '',
				},
				//封禁校验规则
				banUserRules: {
					username: [{required: true, message:"请输入用户名", trigger:'blur'}],
					banTime: [{required: true, validator: validateBanTime, trigger:'blur'}],
					banMsg: [{required: true, message:"请输入封禁原因", trigger:'blur'}]
				},
				//封禁校验规则
				unBanUserRules: {
					username: [{required: true, message:"请输入用户名", trigger:'blur'}],
				},
				//日期时间选择器操作
				pickerOptions: {
				　　disabledDate(time) {
				　　　　return time.getTime() < Date.now() - 24 * 60 * 60 * 1000 || time.getTime() > Date.now() + 5 * 365 * 24 * 60 * 60 * 1000;
				　　}
				},
				unBanDialogVisible: false,
			}
		},
		methods: {
			/**
			 * 封禁账号
			 * @param {Object} formName
			 */
			banUserAction(formName){
				this.$refs[formName].validate(valid => {
					const data = {
						username: this.banUserForm.username,
						banTime: parseInt(this.banUserForm.banTime.getTime()/1000 - Date.now()/1000),
						banMsg: this.banUserForm.banMsg
					}
					if(valid){
						banUserByName(data).then(res => {
							if(responseStatus(res)){
								return this.$message.success("封禁成功");
							}
						})
					}
				})
			},
			/**
			 * 解封账号
			 * @param {Object} formName
			 */
			unBanUserAction(formName){
				this.$refs[formName].validate(valid => {
					const data = {
						username: this.unBanUserForm.username
					}
					if(valid){
						unBanUserByName(data).then(res => {
							if(responseStatus(res)){
								return this.$message.success("解封成功");
							}
						})
					}
				})
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
			}
		},
		created() {
			this.banUserForm.banTime = new Date();
		}
	}
</script>

<style scoped="scoped">
	#ban-user {
		padding-top: 30px;
	}
	.nav-bread {
		margin-bottom: 10px;
	}
	.el-card {
		max-width: 800px;
	}
</style>
