<template>
	<ul>
		<li :class="activeId == 0? 'active':'category-item'" @click="handleCheckCategory()">
			全部
		</li>
		<li :class="activeId == item.categoryId? 'active':'category-item'" v-for="item in categoryList" @click="handleCheckCategory(item.categoryId)">
			<span>{{item.categoryName}}</span>
			<span>{{item.postNum}}</span>
			<i class="el-icon-plus" v-if="isAdd" @click.stop="handleAddSubscribe(item.categoryId)"></i>
		</li>
		<li v-if="user" class="category-title" @click="isAdd = !isAdd">
			<span v-if="!isAdd">选择关注</span>	
			<span v-else>取消选择</span>	
		</li>
	</ul>	
</template>

<script>
	import { getCategory,addSubscribe } from '@/api/user.js'
	import { responseStatus } from '@/utils/responseStatusUtil'
	export default {
		name: "categoryListComponent",
		data(){
			return {
				categoryList: [],
				isAdd: false,
				activeId: 0,
				user: {}
			}
		},
		methods: {
			
			/* 获得分类列表 */
			handleGetCategoryList(){
				getCategory().then(res => {
					if(responseStatus(res))
						this.categoryList = res.data.data;	
				})
			},
			
			/* 点击关注事件 */
			handleAddSubscribe(categoryId){
				const data = {
					categoryId: categoryId
				}
				addSubscribe(data).then(res => {
					if(responseStatus(res)){
						this.$message.success("关注成功!!!");
						this.isAdd = false;
					}
				})
			},
			
			/* 点击分类显示响应的分类列表 */
			handleCheckCategory(categoryId){
				if(categoryId != null){
					this.activeId = categoryId;
					this.$parent.pageInfo.pageNum = 1;
					this.$parent.pageInfo.pageSize = 10;
					this.$parent.pageInfo.total = 0;
					this.$parent.order = 0;
					this.$parent.categoryId = categoryId;
					this.$parent.isAllPostList = false;
					this.$parent.handleGetCategoryPostList(categoryId);
				}else {
					this.activeId = 0;
					this.$parent.order = 0;
					this.$parent.pageInfo.pageNum = 1;
					this.$parent.pageInfo.pageSize = 10;
					this.$parent.pageInfo.total = 0;
					this.$parent.isAllPostList = true;
					this.$parent.handleGetPostList();
				}
				
			},

			/* 通过分类id跳转到帖子列表 */
			routToPostPage(categoryId,categoryName){
				this.$router.push({path: '/categoryPostList', query: { categoryId: categoryId,categoryName: categoryName}});
			}
		},
		created() {
			this.user = JSON.parse(window.sessionStorage.getItem("user"));
			this.handleGetCategoryList();
		}
	}
</script>

<style scoped>
	.category-container {
		display: flex;
		justify-content: space-between;
	}
	li {
		font-size: 12px;
		margin: 5px;
		padding: 8px 15px;
		display: inline-block;
		background-color: #FFF;
		border-radius: 5px;	
	}

	.category-item:hover {
		color: #FFF;
		cursor: pointer;
		background-color: #00C3FF;
	}
	.category-item:hover > span:nth-child(2) {
		color: #282828;
		cursor: pointer;
		background-color: #FFF;
	}
	
	.category-item span:nth-child(2){
		color: #FFF;
		padding: 0 10px;
		margin: 0 5px;
		border-radius: 20px;	
		background-color: #00C3FF;
	}
	.category-title {
		color: #FFF;
		cursor: pointer;
		background-color: #00C3FF;
	}
	
	.active {
		color: #FFF;
		background-color: #00C3FF;
	}
	.active span:nth-child(2){
		color: #282828;
		margin: 0 5px;
		padding: 0 10px;
		border-radius: 20px;
		background-color: #FFF;
	}
</style>
