<template>
	<el-container class="admin-home-container">
		<!-- 头部 -->
		<el-header class="color-back">
			<h3>论坛后台管理</h3>
			<!-- 下拉菜单 -->
			<el-dropdown @command="handleCommand">
				<el-avatar :size="40" :src="user.headSculpture"></el-avatar>
				<el-dropdown-menu slot="dropdown">
					<el-dropdown-item icon="el-icon-circle-close" command="/home">前台界面</el-dropdown-item>
					<el-dropdown-item icon="el-icon-circle-close" command="/logout" @click.native="handleLogout">退出登录</el-dropdown-item>
				</el-dropdown-menu>
			</el-dropdown>
		</el-header>
		
		<!-- 页面主体 -->
		<el-container>
			<!-- 侧边栏 -->
			<el-aside :width="isCollapse? '64px' : '200px'" class="color-back">
				<!-- 菜单 -->
				<el-menu background-color="#282c34" text-color="#FFFFFF" active-text-color="#409EFF" 
						 :collapse="isCollapse" :collapse-transition="false" router
						 :default-active="$route.path">
					<!-- 侧边栏折叠展开 -->
					<el-menu-item class="toggle-button" @click="toggleCollapse">
						<i class="el-icon-s-fold"></i>
						<span slot="title">菜单</span>
					</el-menu-item>
					<el-submenu index="1">
					    <template slot="title">
					        <i class="el-icon-user"></i>
					        <span>用户管理</span>
					    </template>
					    <el-menu-item-group>
					        <el-menu-item :index="index[0]">
								<i class="el-icon-tickets"></i>
								<span>用户列表</span>
							</el-menu-item>
							<el-menu-item :index="index[1]">
								<i class="el-icon-lock"></i>
								<span>封禁账户</span>
							</el-menu-item>
					    </el-menu-item-group>
					</el-submenu>
					<el-submenu index="2">
					    <template slot="title">
					        <i class="el-icon-document"></i>
					        <span>文章管理</span>
					    </template>
					    <el-menu-item-group>
					        <el-menu-item :index="index[2]">
								<i class="el-icon-tickets"></i>
								<span>文章列表</span>
							</el-menu-item>
							<el-menu-item :index="index[3]">
								<i class="el-icon-delete"></i>
								<span>回收站</span>
							</el-menu-item>
					    </el-menu-item-group>
					</el-submenu>
					<el-submenu index="3">
					    <template slot="title">
					        <i class="el-icon-menu"></i>
					        <span>分类管理</span>
					    </template>
					    <el-menu-item-group>
					        <el-menu-item :index="index[4]">
								<i class="el-icon-tickets"></i>
								<span>分类列表</span>
							</el-menu-item>
					    </el-menu-item-group>
					</el-submenu>
					<el-submenu index="4">
					    <template slot="title">
					        <i class="el-icon-menu"></i>
					        <span>轮播图管理</span>
					    </template>
					    <el-menu-item-group>
					        <el-menu-item :index="index[5]">
								<i class="el-icon-tickets"></i>
								<span>轮播图列表</span>
							</el-menu-item>
							<el-menu-item :index="index[6]">
								<i class="el-icon-tickets"></i>
								<span>添加展示</span>
							</el-menu-item>
					    </el-menu-item-group>
					</el-submenu>
				</el-menu>
			</el-aside>
			<!-- 右侧内容主体 -->
			<el-main>
				<router-view></router-view>
			</el-main>
		</el-container>
	</el-container>
</template>

<script>
	import { logout } from '@/api/login'
	import { responseStatus } from '@/utils/responseStatusUtil'
	export default {
		name: 'adminHome',
		data(){
			return {
				user: {},
				//是否折叠
				isCollapse: true,
				//被激活的链接
				activePath: '/adminUserList',
				//路由路径
				index: [
					'/adminUserList',
					'/banUser',
					'/adminPostList',
					'/trashStation',
					'/categoryList',
					'/adminBannerList',
					'/addBanner',
				]
			}
		},
		created() {
			//this.activePath = window.sessionStorage.getItem('activePath'),
			this.user = JSON.parse(window.sessionStorage.getItem('user'));
		},
		methods: {
			//菜单折叠与展开
			toggleCollapse(){
				this.isCollapse = !this.isCollapse;
			},
			//保存链接的激活状态
			/*saveNavState(activePath){
				window.sessionStorage.setItem('activePath',activePath);
				this.activePath = activePath;
			},*/
			
			/* 下拉菜单处理事件 */
			handleCommand(command){
				this.$router.push(command);
			},
			
			/* 退出登录 */
			handleLogout(){
				logout().then(res =>{
					if(responseStatus(res)){
						window.sessionStorage.clear();
						window.localStorage.clear();
						this.$router.push('login');
					}
				});	
			}
		}
	}
</script>

<style lang="less" scoped="scoped">
	.admin-home-container {
		height: 100%;
		background-color: #EAEDF1;
		/* 头部样式 */
		.el-header {
			display: flex;
			align-items: center;
			justify-content: space-between;
			//标题
			h3 {
				color: #FFFFFF;
				font-size: 25px;
				font-weight: bold;
			}
		}
	}
</style>
