<template>
	<el-card id="setting-container">
		<el-tabs>
			<el-tab-pane label="修改密码">
				<div>
					<el-form ref="updatePasswordForm" :model="updatePasswordForm" :rules="updatePasswordRules">
						<el-form-item prop="newPassword" label="新密码">
							<el-input type="text" placeholder="请输新的密码" v-model="updatePasswordForm.newPassword" prefix-icon="el-icon-lock"/>
						</el-form-item>
						<el-button type="primary" @click="handleUpdatePassword">修改</el-button>
					</el-form>
				</div>
			</el-tab-pane>
		</el-tabs>
	</el-card>
</template>

<script>
	import { logout } from '../../api/login.js'
	import { updatePassword } from '../../api/user.js'
	import { responseStatus } from '../../utils/responseStatusUtil'
	export default {
		name: 'setting',
		data(){
			return {
				updatePasswordForm: {
					newPassword: ''
				},
				updatePasswordRules: {
					newPassword: [{required: true, message: '请输入密码', trigger:'blur'}]
				}	
			}
		},
		methods: {
			handleUpdatePassword(){
				this.$refs.updatePasswordForm.validate(valid => {
					const data = {
						password: this.updatePasswordForm.newPassword
					}
					if(valid){
						updatePassword(data).then(res => {
							if(responseStatus(res)){
								this.$message.success("修改成功！");
								this.handleLogout();
							}
						})
					}
				})
			},
			
			/* 退出登录 */
			handleLogout(){
				logout().then(res =>{
					if(responseStatus(res)){
						window.sessionStorage.clear();
						window.localStorage.clear();
						this.$router.push('/login');
					}	
				})
			},
		}
	}
</script>

<style>
	#setting-container {
		padding: 20px;
	}
</style>
