import axios from 'axios'
import qs from 'qs'

export const getUserList = (data)=>{
	return axios.get('/searchUserByString',{params: data});
}

export const banUserByName = (data)=>{
	return axios.post('/admin/BanByName',qs.stringify(data));
}

export const unBanUserByName = (data)=>{
	return axios.post('/admin/unBanByName',qs.stringify(data));
}

/**
 * 新增分类
 */
export const addCategory = (data)=>{
	return axios.post('/admin/addCategory',data);
}

export const uploadCategoryImg = (data)=>{
	return axios.post('/admin/updateCategoryImage',data);
}

export const updateCategoryName = (data)=>{
	return axios.post('/admin/updateCategoryName',qs.stringify(data));
}

/**
 * 获取分类列表
 */
export const getCategory = (data)=>{
	return axios.get('/getCategory');
}

/**
 * 获取回收站帖子
 */
export const getTrashList = (data)=>{
	return axios.get('/admin/getPost',{params: data});
}

/**
 * 恢复回收站帖子
 */
export const rollBackPost = (data)=>{
	return axios.post('/admin/rollBackPost', qs.stringify({postId: data},{arrayFormat: 'repeat'}));
}

/**
 * 获得帖子列表
 */
export const getPostList = (data)=>{
	return axios.get('/getPost',{params: data});
}

/**
 * 通过帖子ID获得帖子
 */
export const getPostById = (data)=>{
	return axios.get('/getPostById',{params: data});
}

/**
 * 设为精华
 */
export const addEssences = (data)=>{
	return axios.post('/admin/addEssences',qs.stringify(data));
}


/**
 * 
 * 
 * 分类操作
 * 
 * 
 */

/* 删除分类 */
export const deleteCategory = (data)=>{
	return axios.post('/admin/deleteCategory',qs.stringify(data));
}

/* (取消)推荐分类 */
export const recommendCategory = (data)=>{
	return axios.post('/admin/updateCategoryRecommend',qs.stringify(data));
}


/**
 * 
 * 
 * 轮播图管理操作
 * 
 * 
 */

//获取轮播图列表
export const getBanner = ()=>{
	return axios.get('/getBanner');
}

/* 删除轮播图 */
export const deleteBanner = (data)=>{
	return axios.delete('/admin/deleteBanner',{params: data});
}

/* 上传轮播图 */
export const bannerUpload = (data)=>{
	return axios.post('/admin/addBanner',data);
}
