<template>
	<div class="add-banner-container">
		<!-- 导航标签 -->
		<el-breadcrumb separator="/" class="nav-bread">
			<el-breadcrumb-item :to="{ path: '/adminUserList' }">首页</el-breadcrumb-item>
			<el-breadcrumb-item>轮播图管理</el-breadcrumb-item>
			<el-breadcrumb-item>新增展示</el-breadcrumb-item>
		</el-breadcrumb>
		<el-card class="add-banner-form">
			<!-- 用户列表 -->
			<el-form ref="bannerFormRef" :model="bannerForm" :rules="bannerFormRules" style="width: 60%;">
				<el-form-item label="文章ID:" prop="postId">
					<el-input type="text" placeholder="请输入文章ID" v-model="bannerForm.postId"/>
				</el-form-item>
				<el-form-item>
					<el-upload ref="upload" action list-type="picture" 
							   :http-request="handleBannerUpload"
							   :auto-upload="false"
							   :limit="1">
							   <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
							     <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传文件</el-button>
						<div slot="tip">只能上传jpg/png文件，且不超过2MB</div>
					</el-upload>
				</el-form-item>
			</el-form>
		</el-card>
	</div>
</template>

<script>
	import { bannerUpload,getBanner } from '@/api/admin'
	import { responseStatus } from '@/utils/responseStatusUtil'
	export default {
		name: 'addBanner',
		data(){
			return {
				bannerForm: {
					postId: this.$route.query.postId
				},
				bannerFormRules: {
					postId: [{required: true, message:"请输入文章ID", trigger:'blur'}]
				},
				bannerList: []
			}
		},
		methods:{
			handleBannerUpload(file){
				if(this.bannerList.length >= 6)
					return this.$message.warning("最多展示6个轮播图！");
				this.$refs.bannerFormRef.validate(valid => {
					let formData = new FormData();
					formData.append('file', file.file);
					formData.append("postId", this.bannerForm.postId);
					if(valid){
						bannerUpload(formData).then(res => {
							if(responseStatus(res)){
								this.$message.success("上传成功！");
								this.$refs.bannerFormRef.resetFields();
								this.$refs.upload.clearFiles();
							}
						})
					}
				})
			},
			submitUpload() {
				this.$refs.upload.submit();
			},
			/* 获取轮播图数量 */
			handleGetBannerList(){
				getBanner().then(res => {
					if(responseStatus(res)){
						this.bannerList = res.data.data;
					}
				})
			},
		},
		mounted() {
			this.handleGetBannerList();
		}
	}
</script>

<style lang="less" scoped>
	.add-banner-container {
		padding: 0 20px;
		.add-banner-form {
			margin: 20px 0;
			padding: 20px 10px;
		}
	}
</style>
