<template>
	<el-card id="post-container">
		<div slot="header">
			<ul class="post-list-head">
				<li>
					<h3>关注动态</h3>
				</li>
			</ul>
		</div>
			<div class="post-item" v-for="item in postList" @click="routeToPostDetail(item.postId)">
				<div @click.stop="routeToOthersPage(item.userId)">
					<el-avatar :size="40" :src="item.headSculpture"></el-avatar>
				</div>
				<div class="post-info">
					<div class="post-info-title">
						<span>【{{item.categoryName}}】</span>
						<span :style="titleColorRandom(item)">{{item.title}}</span>
						<img v-if="item.commentNum > 20" src="~@/assets/icon/hot.gif"/>
						<img v-if="item.isEssences == 1" src="~@/assets/icon/digest.gif"/>
					</div>
					<div class="post-info-bottom">
						<span>楼主：{{item.userName}}</span>
						<span>{{item.createTime}}</span>
						<span class="hidden-xs-only">丨</span>
						<span>最后发表：{{item.updateTime}}</span>
						<div>
							<span>评论数：{{item.commentNum}}</span>
							<span class="hidden-xs-only">/</span>
							<span>热度: {{item.commentNum * 1234}}</span>
						</div>
					</div>
				</div>	
			</div>
			<p v-if="queryInfo.total === 0" style="font-size: 14px; color: #888888; text-align: center;">还没有帖子</p>
	</el-card>
</template>

<script>
	import { getPostCommentNum, sign } from '@/api/user'
	import { responseStatus } from '@/utils/responseStatusUtil'
	export default {
		name: 'subscribePostComponent',
		props: { postList: {type: Array, requires: true},
				 queryInfo: {type:Object, requires: true},
				 title: {type: String, requires: true}
		},
		data() {
			return {
				color: [
					"#8F2A90",
					"#3C9D40",
					"#EC1282",
					"#EE5023",
					"#EE1B2E",
					"#2B65B7"
				]
			}
		},
		methods:{
			handleGetPostCommentNum(postId){
				const data = {
					postId: postId	
				}
				getPostCommentNum(data).then(res => {
					if(responseStatus(res)){
						return res.data.data;
					}		
				})	
			},
			
			
			/* 跳转到帖子详情 */
			routeToPostDetail(postId){
				this.$router.push({path: '/postDetailPage',query: {postId: postId}});
			},
			
			/* 跳转到其他用户的信息界面 */
			routeToOthersPage(userId){
				this.$router.push({path: '/userCenter',query: {userId: userId}});
			},
			/* 循环遍历不同背景色 */
			titleColorRandom(item) {
			    return {
					color: this.color[Math.floor(Math.random() * 6)]
				}
			}
		}
	}
</script>

<style scoped="scoped">
	#post-container {
		font-size: 14px;
		margin: 20px 0;
		border-top: 3px #68C7FF solid;
		border-radius: 5px;
	}
	.post-list-head {
		margin: 0;
		padding: 5px;
		display: flex;
		font-size: 12px;
		align-items: center;
	}
	.post-list-head h3 {
		margin: 0;
		display: inline;
		font-weight: normal;
	}
	.post-list-head li{
		margin: 0 8px;
		cursor: pointer;
	}
	/**
	 * 帖子列表
	 */
	.post-item {
		display: flex;
		padding: 10px;
		border-bottom: 1px #EAEDF1 solid;
	}
	.post-item:hover {
		cursor: pointer;
		background-color: #EAEDF1;
	}
	
	/**
	 * 帖子信息
	 */
	.post-info {
		padding-left: 10px;
		width: 100%;
	}
	
	/* 标题 */
	.post-info-title span:nth-child(1){
		color: #FFA500;
		font-weight: 600;
	}
	.post-info-title span:nth-child(2){
		font-weight: 600;
	}
	.post-info-title img {
		margin-left: 5px;
	}
	
	/* 底部信息 */
	.post-info-bottom {
		color: #888;
		font-size: 12px;
		margin: 15px 0 0 5px;
	}
	.post-info-bottom > span {
		margin-right: 20px;
	}
	.post-info-bottom > span:nth-child(3) {
		color: #DDD;
	}
	.post-info-bottom div {
		float: right;
		color: #4a4a4a;
		margin-right: 20px;
	}
	.post-info-bottom div span:nth-child(2) {
		margin: 0 10px;
	}
	
	.post-list-pagination {
		margin: 10px 0;
		text-align: right;
	}
	
	@media only screen and (min-width: 350px) and (max-width: 768px) {
		.post-info-bottom {
			margin-top: 10px;
		}
		.post-info-bottom span{
			display: block;
			margin-bottom: 8px;
		}
		.post-info-bottom div{
			float: left;
			color: #808080;
		}
	}
	@media only screen and (max-width: 349px){
		.post-item {
			display: block;
		}
		.post-info {
			padding-left: 0;
			margin-top: 10px;
		}
		.post-info-bottom span{
			display: block;
			margin-bottom: 8px;
		}
		.post-info-bottom div{
			display: none;
		}
	}
</style>
