 window.onload = function () {
		
		        function isWeixin() {
		            var ua = navigator.userAgent.toLowerCase(); //获取判断用的对象
		            if (ua.match(/MicroMessenger/i) == "micromessenger") {
		                return true; //微信打开
		            } else {
		                return false; //不是微信打开
		            }
		        }
		
		        function check() {
		            var userAgentInfo = navigator.userAgent;
		            var Agents = new Array("Android", "SymbianOS", "Windows Phone");
		            var flag = true;
		            for (var v = 0; v < Agents.length; v++) {
		                if (userAgentInfo.indexOf(Agents[v]) > 0) {
		                    flag = false;
		                    break;
		                }
		            }
		            return flag;
		        }
		
		        var flag = check();
		        if (flag) {
		            document.getElementById('openApp').style.display = "none";
		        } else {
					console.log(1111)
					document.getElementById('openApp').style.display = "block";
		            document.getElementById('openApp').onclick = function (e) {
		                if (isWeixin()) {
		                    var modPop = document.getElementById('mod_pop');
		                    modPop.style.display = "block";
		                } else {
		                    if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) { //ios判断
		                        alert("暂时没有ios应用~");
		                    } else if (/(Android)/i.test(navigator.userAgent)) { //Android判断
		                        var state = null;
		                        try {
		                            window.location.href = "bysj://webforum/datastring/";
		                        } catch (e) {
		                        }
		                        if (state) {
		                            window.close();
		                        } else {
		                            window.location.href = "http://47.111.9.152/download/webforum-new.apk";
		                        }
		                    }
		                }
		            };
		        }
		
		    }