<template>
	<div>
		<!-- 头部导航 -->
		<el-breadcrumb separator="/" class="admin-nav">
		  <el-breadcrumb-item :to="{ path: '/adminHome' }">首页</el-breadcrumb-item>
		  <el-breadcrumb-item>文章管理</el-breadcrumb-item>
		  <el-breadcrumb-item>回收站</el-breadcrumb-item>
		</el-breadcrumb>
		<!-- 卡片视图区域 -->
		<el-card class="trash-list-card">
			<div>
				<div class="search-item">
					<el-input placeholder="请输入内容" v-model="keyword">
						<template slot="append">
							<span>搜索</span>
						</template>
					</el-input>
				</div>
				<!-- 分类列表 -->
				<el-table :data="trashList" style="width: 100%">
					<el-table-column prop="postId" label="文章ID"/>
					<el-table-column prop="title" label="文章主题"/>
					<el-table-column prop="categoryName" label="分类"/>
					<el-table-column prop="userName" label="用户名"/>
					<el-table-column label="操作">
						<template slot-scope="scope">
							<el-tooltip effect="dark" content="恢复" placement="top" :enterable="false">
								<el-button type="success" size="mini" icon="el-icon-refresh" @click="handleRollBackPost(scope.row.postId)"></el-button>
							</el-tooltip>
						</template>
					</el-table-column>
				</el-table>
			</div>
			<!-- 分页区域 -->
			<el-pagination class="pagination" :current-page.sync="queryInfo.pageNum" :page-size="queryInfo.pageSize" :total="queryInfo.total"
					layout="total, prev, pager, next, jumper" @current-change="handleCurrentChange">
			</el-pagination>
		</el-card>
	</div>
</template>

<script>
	import { getTrashList,rollBackPost } from '@/api/admin'
	import { getSearchList } from '@/api/user'
	import { responseStatus } from '@/utils/responseStatusUtil'
	export default {
		name: 'trashStation',
		data(){
			return {
				trashList:[],
				//分页信息
				queryInfo: {
					pageNum: 1,		
					pageSize: 10,	
					total: 0		
				},
				keyword: ""
			}
		},
		methods:{
			/**
			 * 获取回收站列表
			 */
			handleGetTrashList(){
				const data = {
					pageNum: this.queryInfo.pageNum,
					pageSize: this.queryInfo.pageSize
				}
				getTrashList(data).then((res) => {
					if(responseStatus(res)){
						this.queryInfo = res.data.data;
						this.trashList = res.data.data.list;
					}
						
				})
			},
			
			/* 恢复帖子 */
			handleRollBackPost(postId){
				const data = [];
				data.push(parseInt(postId));
				rollBackPost(data).then(res => {
					if(responseStatus(res)){
						this.handleGetTrashList();
						return this.$message.success("恢复成功");
						
					}
				})
				
			},
			
			/* 模糊搜索帖子 */
			handleSearchPost(){
				const data = {
					content: this.keyword,
					pageNum: this.queryInfo.pageNum,
					pageSize: this.queryInfo.pageSize
				}
				getSearchList(data).then(res => {
					if(responseStatus(res)){
						this.trashList = res.data.data.list;
						this.queryInfo.pageNum = res.data.data.pageNum;
						this.queryInfo.pageSize = res.data.data.pageSize;
						this.queryInfo.total = res.data.data.total;
					}
				})
				
			},
			
			/* 分页处理 */
			handleCurrentChange(newPage){
				this.queryInfo.pageNum = newPage;
				if(this.keyword == ""){
					this.handleGetTrashList();
				}else{
					this.handleSearchPost();
				}
				
			},
		},
		watch: {
			keyword: function (){
				this.queryInfo.pageNum = 1;
				this.queryInfo.pageSize = 10;
				this.queryInfo.total = 0;
				if(this.keyword == ""){
					this.handleGetTrashList();
				}else{
					this.handleSearchPost();
				}	
			}
		},
		created() {
			this.handleGetTrashList();
		}
	}
</script>

<style lang="less" scoped>
	.admin-nav {
		margin: 0 20px;
	}
	.trash-list-card {
		margin: 20px;
		padding-top: 20px;
		border-radius: 5px;
		.search-item {
			max-width: 500px;
			margin-bottom: 10px;
			
			span {
				cursor: pointer;
			}
		}
	}
	.pagination {
		margin-top: 20px;
		text-align: right;
	}
</style>
