<template>
	<div>
		<el-card>
			<div slot="header">
				<div class="post-detail-header">
					<el-button type="primary" @click="routeToPostingPage">写帖子</el-button>
					<div class="comment-pagination hidden-xs-only">
						<el-pagination :current-page.sync="commentListPage.pageNum" :page-size="commentListPage.pageSize" :total="commentListPage.total"
							layout="total,prev, pager, next, jumper" @current-change="handleCurrentChange">
						</el-pagination>
					</div>
				</div>
			</div>
			<!-- 帖子详情 -->
			<post-detail-component v-if="post.postId != null"  :post="post"></post-detail-component>
			<!-- 评论详情 -->
			<post-comment-component v-if="post.postId != null"  :comment="comment"  :isPostUser="isPostUser" :index="index+(commentListPage.pageNum-1)*10" v-for="(comment,index) in commentList" :key="index" v-on:freshCommentList="handleGetCommentList"></post-comment-component>
		</el-card>
		<div class="comment-pagination">
			<el-pagination :current-page.sync="commentListPage.pageNum" :page-size="commentListPage.pageSize" :total="commentListPage.total"
					layout="prev, pager, next, jumper" @current-change="handleCurrentChange">
			</el-pagination>
		</div>
		
		<el-card id="posting-container" v-if="post.postId != null">
			<!-- 发布评论-->
			<div>
				<el-button type="primary" @click="handleUploadComment">发表</el-button>
				<mavon-editor ref="md" id="mavon-editor" :subfield="false" v-model="editContent"  @imgAdd="imgAdd" @imgDel="imgDel"></mavon-editor>
			</div>
		</el-card>
		
	</div>
</template>

<script>
	import postDetailComponent from './post_detail/postDetailComponent.vue'
	import postCommentComponent from './post_detail/postCommentComponent.vue'
	import {uploadComment, uploadCommentImages, getPostById, getComments, getUser } from '@/api/user'
	import { responseStatus } from '@/utils/responseStatusUtil'
	import qs from 'qs'
	export default {
		name: "postDetailPage",
		inject: ['reload'],
		data(){
			return {
				postId: this.$route.query.postId,
				user: {},			//登录用户
				post: {},			//帖子
				commentList: [],	//评论列表
				commentListPage: {
					pageNum: 1,
					pageSize: 10,
					total: 0
				},
				hasPost: '',
				//markdown里的内容
				img_file: {},
				position: [],
				editContent: '',
			}
		},
		computed: {
			isPostUser: function(){
				if(this.user != null){
					return this.post.userId == this.user.userId;
				}
				return false
				
			}
		},
		methods:{
			
			/* 获取帖子信息 */
			handleGetPost(){
				const data = {
					postId: this.postId
				}
				getPostById(data).then(res => {
					if(responseStatus(res)){
						this.post = res.data.data;
					}	
				})
			},
			
			/* 获取评论信息 */
			handleGetCommentList(){
				const data = {
					postId: this.postId,
					pageNum: this.commentListPage.pageNum,
					pageSize: this.commentListPage.pageSize
				}
				getComments(data).then(res => {
					if(responseStatus(res)){
						this.commentList = res.data.data.list;
						this.commentListPage.pageNum = res.data.data.pageNum;
						this.commentListPage.pageSize = res.data.data.pageSize;
						this.commentListPage.total = res.data.data.total;
						for(let index in this.commentList){
							let userdata = {userId: this.commentList[index].userId }
							getUser(userdata).then(res => {
								if(responseStatus(res))
									this.$set(this.commentList[index],'userInfo',res.data.data);		
							})
						}
					}	
				})
			},
			
			/* 添加图片 */
			imgAdd(pos, $file){
				// 缓存图片信息
				this.img_file[pos] = $file;
			},
			imgDel(pos){
				
			},
			/*上传评论 */
			handleUploadComment(){
				if(this.user == null) return this.$message.warning("请先登录！");
				if(this.editContent == '') return this.$message.error("内容不能为空!!!");
				//发布图片
				let formdata = new FormData();
				let imagesUrl = [];
				const position = [];	//图片位置标识符
				for(var i in this.img_file){
					formdata.append("images", this.img_file[i]);
					position.push(i);
				}
				uploadCommentImages(formdata).then(res => {
					if(responseStatus(res)){
						imagesUrl = res.data.data;
						for(let x in position)
							this.$refs.md.$img2Url(position[x].toString(), imagesUrl[x]);
						//发布帖子
						let urlFormdata = new FormData();
						urlFormdata.append("postId", this.post.postId);
						urlFormdata.append("content", this.editContent);
						urlFormdata.append("urls",imagesUrl);
						uploadComment(urlFormdata).then(res => {
							if(responseStatus(res)){
								this.$message.success("发布成功!!!");
								this.editContent = '';
								this.reload();
							}
						})
					}
				})
				
			},

			/* 分页处理  */
			handleCurrentChange(newPage){
				this.commentListPage.pageNum = newPage;
				this.handleGetCommentList();
			},
			/* 跳转到发表帖子页面 */
			routeToPostingPage(){
				if(this.user == null){
					return this.$message.warning("请先登录！");
				}
				this.$router.push('/postingPage');
			},
		},
		components:{
			postDetailComponent,
			postCommentComponent
		},
		created() {
			this.user = JSON.parse(window.sessionStorage.getItem('user'));
			this.handleGetPost();
			this.handleGetCommentList();
		}
	}
</script>

<style lang="less" scoped>
	.post-detail-header {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	
	#posting-container {
		margin-top: 30px;
		padding-top: 20px;
		
		#mavon-editor {
			min-width: 100px;
			margin-top: 15px;
		}
		
		.comment-pagination {
			text-align: right;
			margin-top: 10px;
		}
	}
	.comment-pagination {
		text-align: right;
		margin: 10px 0;
	}
</style>
