<template>
	<div>
		<!-- xs尺寸用户信息 -->
		<div class="detail-item-user-mobile hidden-sm-and-up">
			<header class="item-user-mobile">
				<div>
					<div @click="routeToOthersPage(post.userId)" style="cursor: pointer;">
						<el-avatar :size="50" :src="post.headSculpture"/>
					</div>
					<div class="item-user-info-mobile">
						<p :style="usernameColor">{{post.userName}}</p>
						<span @click="routeToOthersPage(post.userId)">粉丝：{{fansNum}}</span>
						<span @click="routeToOthersPage(post.userId)">关注：{{followNum}}</span>
						<span @click="routeToOthersPage(post.userId)">帖子数：{{postNum}}</span>
					</div>
				</div>
				<p style="font-size: 12px;">楼主</p>
			</header>
		</div>
		<el-row type="flex" id="detail-container">
			<!-- 用户信息 -->
			<el-col class="detail-item-user hidden-xs-only" :xs="24" :sm="5" :md="5" :lg="4" :xl="4">
				<div class="item-user-info">
					<div @click="routeToOthersPage(post.userId)">
						<el-avatar :size="100" :src="post.headSculpture"/>
						<p :style="usernameColor">{{post.userName}}</p>
					</div>
					<table style="width: 100%;">
						<tr>
							<th @click="routeToOthersPage(post.userId)">
								<p>{{followNum}}</p>
								<span>关 注</span>
							</th>
							<th @click="routeToOthersPage(post.userId)">
								<p>{{fansNum}}</p>
								<span>粉丝数</span>
							</th>
							<th @click="routeToOthersPage(post.userId)">
								<p>{{postNum}}</p>
								<span>帖 子</span>
							</th>
						</tr>
					</table>
					<div class="detail">
						<span v-if="user.role == 2" class="color-yellow">论坛管理员</span>
						<div>
							<div class="detail-item">
								<label>性别：</label>
								<span v-if="user.sex == 0">保密<i class="iconfont icon-suo color-suo"/></span>
								<span v-if="user.sex == 1">男<i class="iconfont icon-nan color-nan"/></span>
								<span v-if="user.sex == 2">女<i class="iconfont icon-nv color-nv"/></span>
							</div>
							<div class="detail-item">
								<label>QQ：</label>
								<span>{{user.qq ? user.qq : "暂未填写"}}</span>
							</div>
							<div class="detail-item">
								<label>微信：</label>
								<span>{{user.wechat ? user.wechat : "暂未填写"}}</span>
							</div>
							<div class="detail-item">
								<label>Email：</label>
								<span>{{user.email ? user.email : "暂未填写"}}</span>
							</div>
							<div class="detail-item">
								<label>字数：</label>
								<span>{{user.wordNumber}}</span>
							</div>
						</div>
						<div>
							<span>个性签名：{{user.personalizedSignature}}</span>
						</div>
					</div>
				</div>
			</el-col>
			<el-col :xs="24" :sm="19" :md="19" :lg="20" :xl="20">
				<div class="detail-item-content">
					<!-- 内容头部 -->
					<div class="detail-item-header">
						<div style="text-align: right;">
							<span class="hidden-xs-only" style="font-size: 12px;">楼主</span>
						</div>
						<h3 :style="titleColor">
							{{post.title}}
							<img v-if="post.commentNum > 20" src="~@/assets/icon/hot.gif"/>
							<img v-if="post.isEssences == 1" src="~@/assets/icon/digest.gif"/>
							<el-tag type="warning" style="float: right;">{{post.categoryName}}</el-tag>
						</h3>
						<span>
							<i class="el-icon-date"></i>
							&nbsp;发表于&nbsp; {{post.createTime}}
						</span>
					</div>
					<div>
						<mavon-editor :value="post.content" defaultOpen="preview" :boxShadow="false" :editable="false"
							:subfield="false" :toolbarsFlag="false" previewBackground="#FFF">
						</mavon-editor>
					</div>
					<div v-if="!isMTE(post.conetent)">
						<img v-for="item in images" :src="item" width="100%" height="100%"/>
					</div>
					<div>
						<!-- 点赞功能 -->
						<div :style="isGood ? 'color: #00C3FF' : 'color: #808080' " class="like">
							<div style="display: inline-block;" @click="isGood ? handleCancelGoodPost(post.postId) : handleSetGoodPost(post.postId)">
								<i class="el-icon-s-opportunity"></i>
								<p class="text-size-normal">点 赞</p>
							</div>
						</div>
					</div>
				</div>
			</el-col>
		</el-row>
	</div>
	
</template>

<script>
	import { getUser,getPostImage,getFollowNum,getFans,getPostNum } from '@/api/user'
	import { setGoodPost, cancelGoodPost, checkGoodPost } from '@/api/api_post'
	import { responseStatus } from '@/utils/responseStatusUtil'
	import { textColorRandom } from '@/utils/colorUtils'
	export default {
		name: 'postDetailComponent',
		props: { post: {type: Object, requires: true}},
		data() {
			return {
				user: {},
				followNum: 0,
				fansNum: 0,
				postNum: 0,
				images: [],
				isGood: false,		//是否点赞
				isToken: false		//判断用户是否登录
			}
		},
		computed: {
			/* 用户名颜色 */
			usernameColor: function(){
				return {
					color: textColorRandom()
				}
			},
			/* 标题颜色 */
			titleColor: function(){
				return {
					color: textColorRandom()
				}
			}
		},
		methods:{
			handleGetUserInfo(){
				if(!this.post) return;
				const data = {
					userId: this.post.userId
				}
				getUser(data).then(res => {
					if(responseStatus(res)){
						this.user = res.data.data;
					}
				})
			},
			
			/* 获得关注数 */
			handleGetFollowNum(){
				const data = {
					userId: this.post.userId
				}
				getFollowNum(data).then(res => {
					if(responseStatus(res))
						this.followNum = res.data.data; 
				})  	
			},
			
			/* 获取粉丝数 */
			handleGetFansNum(){
				const data = {
					userId: this.post.userId
				}
				getFans(data).then(res => {
					if(responseStatus(res))
						this.fansNum = res.data.data; 
				})
			},
			
			/* 获取发表的帖子数 */
			handleGetPostNum(){
				const data = {
						userId: this.post.userId
					}
				getPostNum(data).then(res => {
					if(responseStatus(res))
						this.postNum = res.data.data; 
				})
			},
			
			/* 获取图片 */
			handleGetPostImage(){
				if(this.isMTE()) return;
				const data = {
					postId: this.post.postId
				}
				getPostImage(data).then(res => {
					if(responseStatus(res))
						this.images = res.data.data;
				})
			},
			
			/* 判断文本类型 */
			isMTE(){
				if(this.post.content == null) return false
				if(this.post.content.search(/\[[\s\S]*?\]\([\s\S]*?\)/) != -1){
					return true;
				}else{
					return false;
				}
			},
			
			/**
			 * 点赞功能
			 */
			
			/* 用户点赞 */
			handleSetGoodPost(postId) {
				const data = { postId: postId };
				setGoodPost(data).then(res => {
					if(responseStatus(res)){
						this.$message.success("点赞成功!!!");
						this.handleCheckGoodPost(postId);
						console.log(this.post);
					}
				});
			},
			
			/* 取消点赞 */
			handleCancelGoodPost(postId) {
				const data = { postId: postId };
				cancelGoodPost(data).then(res => {
					if(responseStatus(res)){
						this.isGood = false;
						this.$message.success("取消成功!!!");
						this.handleCheckGoodPost(postId);
						console.log(this.post);
					}
				});
			},
			
			/* 检查是否点赞 */
			handleCheckGoodPost(postId){
				const data = { postId: postId };
				checkGoodPost(data).then(res => {
					if(responseStatus(res)){
						let result = res.data.data;
						if(result === 1){
							this.isGood = true;
						}
					}
				});
			},
			
			/**
			 * 用户登录
			 */
			handleIsToken() {
				if(window.localStorage.getItem('accessToken')) {
					this.isToken = true;
				}else{
					this.isToken = false;
				}
			},
			

			
			/* 跳转到其他用户的信息界面 */
			routeToOthersPage(userId){
				this.$router.push({path: '/userCenter',query: {userId: userId}});
			},
		},
		created() {
			this.handleIsToken();
			this.handleGetUserInfo();
			this.handleGetPostImage();
			if(this.isToken){
				this.handleCheckGoodPost(this.post.postId);
			}
			this.handleGetFollowNum();
			this.handleGetPostNum();
			this.handleGetFansNum();
			
		}
	}
</script>


<style lang="less" scoped>
	
	#detail-container {
		border-bottom: 1px #DDDDDD solid;
		
		/* 用户信息样式 */
		.detail-item-user {
			padding: 20px;
			font-size: 14px;
			text-align: center;
			background-color: #F8F8F8;
			
			.item-user-info table th:hover {
				color: #409EFF;
				cursor: pointer;
				text-decoration: underline;
			}
			.item-user-info div> p {
				font-size: 14px;
				font-weight: 600;
				margin: 10px 0;
			}
			.item-user-info table {
				font-size: 12px;
			}
			.detail {
				font-size: 12px;
				margin-top: 10px;
				text-align: left;
				.detail-item {
					margin: 5px 0;
					display: flex;
					justify-content: space-between;
					white-space: nowrap;
					text-overflow: ellipsis;
					overflow-x: hidden;
				}
			}
			
		}
		.detail-item-content {
			padding: 10px 20px;
			
			.detail-item-header {
				margin-bottom: 10px;
				h3 {
					margin: 10px 0;
				}		
			}
			.detail-item-header > span {
				font-size: 12px;
				color: #888;
			}
			//帖子点赞
			.like {
				margin: 20px 0px;
				text-align: center;
				cursor: pointer;
			}
		}
	}
	
	
	/**
	 * xs样式
	 */
	
	/* 用户信息样式 */
	.detail-item-user-mobile {
		padding: 10px;
		font-size: 14px;
		background-color: #F8F8F8;
		
		.item-user-mobile {
			display: flex;
			justify-content: space-between;
		}
		
		.item-user-mobile > div {
			display: flex;
			align-items: center;
			//用户信息
			.item-user-info-mobile {
				margin-left: 10px;
				p {
					font-size: 14px;
					font-weight: 600;
				}
				span {
					font-size: 12px;
					color: #888888;
					display: inline-block;
					margin-right: 10px;
					margin-top: 5px;
				}
				span:hover {
					color: #409EFF;
					cursor: pointer;
					text-decoration: underline;
				}
			}
		}
	}
	
	
</style>
