<template>
	<el-row>
		
		<!-- 热帖排行 -->
		<el-col :xs="24" :sm="7" :md="7" :lg="7" :xl="7" class="hot-container">
			<ul>
				<div class="ul-header">
					<span>热帖排行</span>
					<div class="refresh-button">
						<i class="el-icon-refresh"></i>
						<span @click="handleRefreshHot">换一批</span>
					</div>
				</div>
				<li class="hot-item" v-for="(item,index) in hotPostList">
					<div class="hot-item-left">
						<span style="color: #FE2D46;" class="index" v-if="index == 0">{{ index+1 }}</span>
						<span style="color: #FF6600;" class="index" v-if="index == 1">{{ index+1 }}</span>
						<span style="color: #FAA90E;" class="index" v-if="index == 2">{{ index+1 }}</span>
						<span class="index" v-if="index > 2">{{ index+1 }}</span>
						<span class="title" @click="routeToPostDetailPage(item.postId)">{{item.title}}</span>
					</div>
					<div style="color: #808080; font-size: 12px;">
						<span>{{item.commentNum}}</span>
						<i class="el-icon-chat-dot-square"></i>
					</div>
				</li>
			</ul>
		</el-col>		
	</el-row>
</template>

<script>
	import { getRandomPosts } from '@/api/api_post'
	import { responseStatus } from '@/utils/responseStatusUtil'
	export default {
		data(){
			return {
				hotPostList: [],
			}
		},
		methods: {
			
			
			/* 获取热门数据 */
			handleGetHotList(){
				getRandomPosts().then(res => {
					if(responseStatus(res)){
						this.hotPostList = res.data.data; 
					}
				})
			},
			
			/* 换一换 */
			handleRefreshHot(){
				this.handleGetHotList();
			},
			
			/* 跳转到帖子详情 */
			routeToPostDetailPage(postId){
				if(postId != null)
					this.$router.push({path: '/postDetailPage',query: {postId: postId}});
			}

		},
		mounted() {
			this.handleGetHotList();
		}
	}
</script>

<style lang="less" scoped>
	
	/* 热帖排行 */
	.hot-container {
		width: 100%;
		max-height: 400px;
		background-color: #FFF;
		font-size: 14px;
		padding: 10px 20px;
		/* 头部 */
		ul {
			margin: 0;
			.ul-header{
				display: flex;
				justify-content: space-between;
				margin-bottom: 15px;
				.refresh-button {
					i {
						color: #4E6EF2;
						font-weight: bold;
					}
					:hover {
						cursor: pointer;
						color: #409EFF;
					}
				} 
			}
			.no-hot {
				color: #808080;
				font-size: 12px;
				text-align: center;
			}
			/* 内容 */
			.hot-item {
				padding: 5px;
				display: flex;
				justify-content: space-between;
				
				.hot-item-left {
					max-width: 300px;
					overflow: hidden;
					white-space: nowrap;
					text-overflow:ellipsis;
					.index {
						font-size: 15px;
						color: #808080;
						font-weight: 500;
						margin-right: 5px;
					}
					.title {
						color: #2440B3;
					}
					.title:hover {
						color: #409EFF;
						cursor: pointer;
					}
				}	
			}
		}	
	}
	
	@media only screen and (max-width: 768px) {
		.hot-container {
			max-width: 100%;
			float: left;
		}
		.hot-item {
			max-width: 100%;
		}
	}
	
</style>
