<template>
	<div class="posting-container">
		<div class="posting-content">
			<el-form :label-position="labelPosition" ref="postForm" :model="postForm" :rules="postRules" label-width="60px">
				<el-form-item prop="category" label="分类:">
					<el-radio-group v-model="postForm.category" v-for="item in categoryList" @change="radioChange(item.categoryId)">
					    <el-radio-button :label="item.categoryName" style="margin: 5px;"></el-radio-button>
					</el-radio-group>
				</el-form-item>
				<el-form-item prop="title" label="标题:">
					<el-input v-model="postForm.title"/>
				</el-form-item>
				<el-form-item  prop="content" label="内容:">
					<mavon-editor ref="md"  defaultOpen="edit" v-model="postForm.content" @imgAdd="imgAdd" @imgDel="imgDel" @save="handleUploadPost()"></mavon-editor>
				</el-form-item>
				<el-form-item>
				    <el-button type="primary" @click="handleUploadPost()">提交</el-button>
				    <el-button @click="handleReset()">重置</el-button>
				</el-form-item>
			</el-form>
		</div>
		
	</div>
</template>

<script>
	import { getCategoryList,uploadPost,uploadImage } from '../../api/user'
	import { responseStatus } from '../../utils/responseStatusUtil'
	export default {
		name: 'posting',
		data(){
			return {
				categoryList: [],
				postForm: {
					title: '',
					categoryId: 0,
					category: '',
					content: '',
				},
				postRules: {
					category: [{required: true, message:"分类不为空", trigger:'blur'}],
					title: [{required: true, message:"标题不为空", trigger:'blur'}],
					content: [{required: true, message: '内容不为空', trigger:'blur'}]
				},
				img_file: {},
				windowWidth: 0,
				labelPosition: 'left'
			}
		},
		methods: {
			/**
			 * 获取全部分类
			 */
			handleGetCategory(){
				getCategoryList().then(res => {
					if(responseStatus(res))
						this.categoryList = res.data.data; 	
				})
			},
			/**
			 * 分类改变
			 */
			radioChange(categoryId){
				this.postForm.categoryId = categoryId;
			},
			/**
			 * 添加删除图片
			 * @param {Object} pos
			 * @param {Object} $file
			 */
			imgAdd(pos, $file){
				if(this.user == null){
					return this.$message.warning("请先登录！");
				}
				// 缓存图片信息
				this.img_file[pos] = $file;
				
			},
			imgDel(pos){
				
			},
			/**
			 * 上传评论
			 */
			handleUploadPost(){	
				if(this.user == null){
					return this.$message.warning("请先登录！");
				}
				this.$refs.postForm.validate(valid => {
					if(valid){
						//发布图片
						let formdata = new FormData();
						let imagesUrl = [];
						const position = [];	//图片位置标识符
						for(var i in this.img_file){
							formdata.append("images", this.img_file[i]);
							position.push(i);
						}
						uploadImage(formdata).then(res => {
							if(responseStatus(res)){
								imagesUrl = res.data.data;
								for(let x in position)
									this.$refs.md.$img2Url(position[x].toString(), imagesUrl[x]);		
								//发布帖子
								let urlFormdata = new FormData();
								urlFormdata.append("title", this.postForm.title);
								urlFormdata.append("content",  this.postForm.content);
								urlFormdata.append("categoryId", this.postForm.categoryId);
								urlFormdata.append("urls",imagesUrl)
								uploadPost(urlFormdata).then(res => {
									if(responseStatus(res)){
										this.$message.success("发布成功!!!");
										this.$refs.postForm.resetFields();
										this.$router.push('/postListPage');
									}
									
								})		
							}
							
							
						})
					}
				})
			},
			
			handleReset(){
				this.postForm.categoryId = 0
				this.$refs.postForm.resetFields();
			}
		},
		watch:{
			windowWidth (val) {
				if(this.windowWidth < 400)
					this.labelPosition = 'top';
				else
					this.labelPosition = 'left';
			}
		},
		mounted() {
			var that = this;
			    // <!--把window.onresize事件挂在到mounted函数上-->
				window.onresize = () => {
			        return (() => {
						window.fullWidth = document.documentElement.clientWidth;
			            that.windowWidth = window.fullWidth; // 宽
			        })()
			    };
		},
		created() {
			this.windowWidth = document.documentElement.clientWidth;
			this.user = JSON.parse(window.sessionStorage.getItem('user'));
			this.handleGetCategory();
		}
	}
</script>

<style scoped="scoped">
	.posting-container {
		padding: 10px;
	}
</style>
