<template>
	<div class="banner-container">
		<!-- 头部导航 -->
		<el-breadcrumb separator="/" class="admin-nav">
		  <el-breadcrumb-item :to="{ path: '/adminHome' }">首页</el-breadcrumb-item>
		  <el-breadcrumb-item>轮播图管理</el-breadcrumb-item>
		  <el-breadcrumb-item>轮播图列表</el-breadcrumb-item>
		</el-breadcrumb>
		<!-- 卡片视图区域 -->
		<el-card class="banner-list">
			<el-table :data="bannerList" style="width: 100%">
				<el-table-column prop="id" label="图片ID" width="100"/>
				<el-table-column prop="postId" label="文章ID" width="100"/>
				<el-table-column prop="postInfo.title" label="文章标题"/>
				<el-table-column prop="postInfo.categoryName" label="分类"/>
				<el-table-column prop="recommendImage" label="图片地址">
					<template slot-scope="scope">
					　　<img :src="scope.row.recommendImage" width="140" height="80"/>
					</template>
				</el-table-column>
				<el-table-column label="操作">
					<template slot-scope="scope">
						<el-tooltip effect="dark" content="删除" placement="top" :enterable="false">
							<el-button type="danger" size="mini" icon="el-icon-delete" @click="handleDeleteBanner(scope.row.id)"></el-button>
						</el-tooltip>
					</template>
				</el-table-column>
			</el-table>
		</el-card>
	</div>
</template>

<script>
	import { getBanner, getPostById, deleteBanner } from '@/api/admin'
	import { responseStatus } from '@/utils/responseStatusUtil'
	export default {
		name: 'adminBannerList',
		data(){
			return {
				bannerList:[]
			}
		},
		methods:{
			handleGetBannerList(){
				getBanner().then(res => {
					if(responseStatus(res)){
						this.bannerList = res.data.data;
						//获取帖子信息
						for(let index in this.bannerList){
							let data = { postId: this.bannerList[index].postId }
							getPostById(data).then(res => {
								if(responseStatus(res))
									this.$set(this.bannerList[index],'postInfo',res.data.data);
							})	
						}
					}
				})
			},
			handleDeleteBanner(bannerId){
				const data = {
					bannerId: bannerId
				}
				deleteBanner(data).then(res => {
					if(responseStatus(res)){
						this.$message.success("删除成功！");
						this.handleGetBannerList();
					}
				})
			}
		},
		mounted() {
			this.handleGetBannerList();
		}
	}
</script>

<style lang="less" scoped>
	.banner-container {
		padding: 0 20px;
		.banner-list {
			margin: 20px 0;
			padding: 20px 10px;
		}
	}
</style>
