<template>
	<div>
		<el-button id="posting-button" @click="routeToPostingPage">发新帖</el-button>
		
		<!-- 分类列表 -->
		<category-list-component></category-list-component>
		
		<!-- 全部帖子列表 -->
		<post-list :postList="postList" :pageInfo="pageInfo" :order="order" :isAllPostList="isAllPostList"></post-list>
			
		
		
		
		<!-- 分类帖子列表 -->
		
		
	</div>
</template>

<script>
	import categoryListComponent from './post_list_components/categoryListComponent.vue'
	import postListComponent from './post_list_components/postListComponent.vue'
	import { getCategoryPostList,getPostList } from '../../../api/user.js'
	import { responseStatus } from '../../../utils/responseStatusUtil'
	export default{
		name: 'postListPage',
		components:{
			categoryListComponent: categoryListComponent,
			postList: postListComponent
		},
		data(){
			return {
				pageInfo: {
					pageNum: 1,
					pageSize: 10,
					total: 0
				},
				postList: [],
				order: 0,
				categoryId: null,
				isAllPostList: true
			}	
		},
		methods: {
			
			/* 获得全部帖子列表 */
			handleGetPostList(){
				const data = {
					pageNum: this.pageInfo.pageNum,
					pageSize: this.pageInfo.pageSize,
					orderBy: this.order
				}
				getPostList(data).then(res => {
					if(responseStatus(res)){
						this.pageInfo.pageNum = res.data.data.pageNum;
						this.pageInfo.total = res.data.data.total;
						this.postList = res.data.data.list; 
					}
				})
			},
			
			
			/* 获得分类帖子列表 */
			handleGetCategoryPostList(categoryId){
				const data = {
					categoryId:this.categoryId,
					pageNum: this.pageInfo.pageNum,
					pageSize: this.pageInfo.pageSize,
					orderBy: this.order
				}
				getCategoryPostList(data).then(res => {
					if(responseStatus(res)){
						this.pageInfo.pageNum = res.data.data.pageNum;
						this.pageInfo.total = res.data.data.total;
						this.postList = res.data.data.list; 
					}
				})
			},
			
			
			/* 跳转到详情页面 */
			routeToPostDetail(post){
				this.$router.push({path: '/postDetailPage',query: {postId: postId}});
			},
			
			/* 跳转到发表帖子界面 */
			routeToPostingPage(){
				this.$router.push('/postingPage');
			}
		},
		created() {
			this.handleGetPostList();
		}
		
	}
</script>

<style scoped>
	.post-list-container {
		margin: 10px 0;
		display: flex;
		justify-content: space-between;
	}
	#posting-button {
		color: #FFF;
		background-color: #68C7FF; 
		margin-left: 5px;
		border: none;
	}
	
</style>
