import { Message } from 'element-ui'


export const  responseStatus = (res)=>{
	if(res.status != 200){
		Message({ type: 'error', message: '请求失败', duration: 1000 });
		return false;
	}
	if(res.data.status != 10000){
		//响应状态码判断
		if(res.data.status == 10020){
			console.log(res);
			Message.warning("没有登陆或登录状态过期，请重新登录！");
			window.localStorage.removeItem('accessToken');
			return false;
		}
		Message({ type: 'error', message: res.data.msg, duration: 1000 });
		console.log(res);
		return false;
	}
	
	return true;
}