<template>
	<div v-if="comment.userInfo != null">
		<!-- xs尺寸用户信息 -->
		<div class="detail-item-user-mobile hidden-sm-and-up">
			<header class="item-user-mobile">
				<div>
					<div @click="routeToOthersPage(comment.userId)">
						<el-avatar :size="50" :src="comment.headSculpture"/>
					</div>
					<div class="item-user-info-mobile">
						<p :style="usernameColor">{{comment.username}}</p>
						<span @click="routeToOthersPage(comment.userId)">粉丝：{{fansNum}}</span>
						<span @click="routeToOthersPage(comment.userId)">关注：{{followNum}}</span>
						<span @click="routeToOthersPage(comment.userId)">帖子数：{{postNum}}</span>
					</div>
				</div>
				<p style="font-size: 12px;">{{index+1}}楼</p>
			</header>
		</div>
		<el-row type="flex" id="comment-container">
				<!-- 用户信息 -->
				<el-col class="comment-item-user hidden-xs-only" :xs="24" :sm="5" :md="5" :lg="4" :xl="4">
					<div class="item-user-info">
						<div @click="routeToOthersPage(comment.userId)">
							<el-avatar :size="100" :src="comment.headSculpture" style="cursor: pointer;"></el-avatar>
							<p :style="usernameColor">{{comment.username}}</p>
						</div>
						<table style="width: 100%;">
							<tr>
								<th @click="routeToOthersPage(comment.userId)">
									<p>{{followNum}}</p>
									<span>关 注</span>
								</th>
								<th @click="routeToOthersPage(comment.userId)">
									<p>{{fansNum}}</p>
									<span>粉丝数</span>
								</th>
								<th @click="routeToOthersPage(comment.userId)">
									<p>{{postNum}}</p>
									<span>帖 子</span>
								</th>
							</tr>
						</table>
						<div class="detail">
							<span v-if="comment.userInfo.role == 2" class="color-yellow">论坛管理员</span>
							<div>
								<div class="detail-item">
									<label>性别：</label>
									<span v-if="comment.userInfo.sex == 0">保密<i class="iconfont icon-suo color-suo"/></span>
									<span v-if="comment.userInfo.sex == 1">男<i class="iconfont icon-nan color-nan"/></span>
									<span v-if="comment.userInfo.sex == 2">女<i class="iconfont icon-nv color-nv"/></span>
								</div>
								<div class="detail-item">
									<label>QQ：</label>
									<span>{{comment.userInfo.qq ? comment.userInfo.qq : "暂未填写"}}</span>
								</div>
								<div class="detail-item">
									<label>微信：</label>
									<span>{{comment.userInfo.wechat ? comment.userInfo.wechat : "暂未填写"}}</span>
								</div>
								<div class="detail-item">
									<label>Email：</label>
									<span>{{comment.userInfo.email ? comment.userInfo.email : "暂未填写"}}</span>
								</div>
								<div class="detail-item">
									<label>字数：</label>
									<span>{{comment.userInfo.wordNumber}}</span>
								</div>
							</div>
							<div>
								<span>个性签名：{{comment.userInfo.personalizedSignature}}</span>
							</div>
						</div>
					</div>	
				</el-col>
				<el-col :xs="24" :sm="19" :md="19" :lg="20" :xl="20">
					<div class="comment-item-detail">
						<!-- 内容头部 -->
						<div class="comment-item-header">
							<div>
								<span class="hidden-xs-only" style="font-size: 12px;">{{index+1}}楼</span>
							</div>
							<span style="color: #888;">
								<i class="el-icon-date"></i>
								&nbsp;发表于&nbsp; {{comment.commentTime}}
							</span>
							<a class="reply" @click="replyDialogVisible = true">回复</a>
						</div>
						<div>
							<mavon-editor :value="comment.content" defaultOpen="preview" :boxShadow="false" :editable="false"
								:subfield="false" :toolbarsFlag="false" previewBackground="#FFF">
							</mavon-editor>
						</div>
						<div v-if="!isMTE(comment.content)">
							<img v-for="item in images" :src="item" width="100%" height="100%"/>
						</div>
						<div class="comment-item-bottom">
							<el-dropdown size="mini" @command="handleCommand" v-if="isPostUser||isCommentUser">
							  <span style="font-size: 12px; color: #666666;">
							    管理操作<i class="el-icon-arrow-down el-icon--right"></i>
							  </span>
							  <el-dropdown-menu slot="dropdown">
							    <el-dropdown-item command="delete">删除评论</el-dropdown-item>
							  </el-dropdown-menu>
							</el-dropdown>
							<span style="color: #BBB; margin: 0 5px;">丨</span>
							<span>举报</span>
						</div>
						
						<!-- 帖子回复容器 -->
						<ul class="reply-container">
							<li v-for="item in replyList">
								<el-avatar class="avatar-hover" :size="30" :src="item.headSculpture" @click.native="routeToOthersPage(comment.userId)"/>
								<div class="reply-detail">
									<div>
										<span class="color-blue">回复：</span>
										<span>{{item.content}}</span>
									</div>
								</div>
							</li>
						</ul>
						<!-- 帖子回复容器 结束-->
						
					</div>
				</el-col>
		</el-row>
		
		<!-- 回复窗口 -->
		<el-dialog title="回复消息" :visible.sync="replyDialogVisible" @close="replyDialogClosed" width="80%">
			<!-- 回复人信息 -->
			<div class="reply-info">
				<div>
					<span>{{comment.username}}</span>
					<span class="text-size-small">{{comment.commentTime}}</span>
				</div>
				<span>{{comment.content}}</span>
			</div>
			<el-button type="primary" @click="handleReply(comment.commentId,replyContent)">回复</el-button>
			<mavon-editor v-model="replyContent" defaultOpen="edit" :toolbars="toolbars" placeholder="输入回复的消息..."/>
		</el-dialog>
		<!-- 回复窗口 结束 -->
	</div>
	
</template>

<script>
	import {deleteMyComment, getPostImage,getFollowNum,getFans,getPostNum,getReplyList,reply } from '@/api/user'
	import { responseStatus } from '@/utils/responseStatusUtil'
	import { textColorRandom } from '@/utils/colorUtils'
	export default {
		name: 'postCommentComponent',
		props: { 
			comment: {type: Object, requires: true},
			index: {type: Number, requires: true},
			isPostUser: {type: Boolean, requires: true}
		},
		data() {
			return {
				toolbars: {
					bold: true, // 粗体
					italic: true,// 斜体
					header: true,// 标题
					underline: true, // 下划线
					strikethrough: true, // 中划线
					mark: true, // 标记
					superscript: true, // 上角标
					subscript: true, // 下角标
					quote: true, // 引用
					ol: true, // 有序列表
					ul: true, // 无序列表
					link: true, // 链接
					imagelink: true, // 图片链接
					subfield: false, // 单双栏模式
				},
				followNum: 0,	//关注数
				fansNum: 0,		//粉丝数
				postNum: 0,		//帖子数
				images: [],
				user: {}	,//登录的用户
				replyList: [],
				replyContent: "",
				replyPageInfo: {
					pageNum: 1,
					pageSize: 10,
					total: 0
				},
				replyDialogVisible: false
			}
		},
		computed: {
			/* 用户名颜色 */
			usernameColor: function(){
				return {
					color: textColorRandom()
				}
			},
			isCommentUser: function(){
				if(this.user != null){
					return this.comment.userId == this.user.userId;
				}
			}
		},
		methods:{
			
			//获取回复信息
			handleGetReplyList(){
				if(!this.comment) return;
				const data = {
					commentId: this.comment.commentId,
					pageNum: this.replyPageInfo.pageNum,
					pageSize: this.replyPageInfo.pageSize
				}
				getReplyList(data).then(res => {
					if(responseStatus(res)){
						this.replyList = res.data.data.list;
						this.replyPageInfo = res.data.data;
					}
				})
				
			},
			
			/* 获得关注数 */
			handleGetFollowNum(){
				const data = {
					userId: this.comment.userId
				}
				getFollowNum(data).then(res => {
					if(responseStatus(res))
						this.followNum = res.data.data; 
				})  	
			},
			
			/* 获取粉丝数 */
			handleGetFansNum(){
				const data = {
					userId: this.comment.userId
				}
				getFans(data).then(res => {
					if(responseStatus(res))
						this.fansNum = res.data.data; 
				})
			},
			
			/* 获取发表的帖子数 */
			handleGetPostNum(){
				const data = {
						userId: this.comment.userId
					}
				getPostNum(data).then(res => {
					if(responseStatus(res))
						this.postNum = res.data.data; 
				})
			},
			
			
			
			/* 获取图片 */
			handleGetPostImage(){
				const data = {
					postId: this.post.postId
				}
				getPostImage(data).then(res => {
					if(responseStatus(res))
						this.images = res.data.data;
				})
			},
			
			/* 删除评论 */
			handleDeleteComment(commentId) {
				const data = {
					commentId: commentId
				}
				deleteMyComment(data).then((res) => {
					if(responseStatus(res)){
						this.$message.success("删除成功");
						this.$emit('freshCommentList');
					}				   							   
				})
			},
			
			//回复用户
			handleReply(commentId,replyContent){
				if(this.user == null) return this.$message.warning("请先登录！");
				if(commentId != null && replyContent != ""){
					const data = {
						commentId: commentId,
						content: replyContent
					}
					reply(data).then(res => {
						if(responseStatus(res)){
							this.replyDialogVisible = false;
							this.$message.success("回复成功！");
							this.handleGetReplyList();
							
						}
					})
				}
			},
			
			//回复框和隐藏
			replyDialogClosed(){
				this.replyContent = "";
			},

			
			isMTE(){
				if(this.comment.content == null) return false
				if(this.comment.content.search(/\[[\s\S]*?\]\([\s\S]*?\)/) != -1){
					return true;
				}else{
					return false;
				}
			},
			/**
			 * 下拉菜单处理事件
			 */
			handleCommand(command){
				if(command == 'delete'){
					//删除评论
					this.handleDeleteComment(this.comment.commentId);
				}
			},
			
			/* 跳转到其他用户的信息界面 */
			routeToOthersPage(userId){
				this.$router.push({path: '/userCenter',query: {userId: userId}});
			},
			
		},
		watch: {
			comment: function() {
				this.handleGetReplyList();
				this.handleGetFollowNum();
				this.handleGetPostNum();
				this.handleGetFansNum();
			}
		},
	 	created() {
			this.user = JSON.parse(window.sessionStorage.getItem('user'));
			this.handleGetReplyList();
			this.handleGetFollowNum();
			this.handleGetPostNum();
			this.handleGetFansNum();		
		}
	}
</script>

<style lang="less" scoped>
	
	#comment-container {
		border-bottom: 1px #DDDDDD solid;
		
		/* 用户信息样式 */
		.comment-item-user {
			padding: 20px;
			font-size: 14px;
			text-align: center;
			background-color: #F8F8F8;
			
			//用户名
			.item-user-info p:nth-child(2) {
				font-weight: 600;
				margin: 10px;
			}
		}
		
		.item-user-info table th:hover {
			color: #409EFF;
			cursor: pointer;
			text-decoration: underline;
		}
		
		.item-user-info table {
			font-size: 12px;
		}
		
		.detail {
			font-size: 12px;
			margin-top: 10px;
			text-align: left;
			.detail-item {
				margin: 5px 0;
				display: flex;
				justify-content: space-between;
				white-space: nowrap;
				text-overflow: ellipsis;
				overflow-x: hidden;
			}
		}
		
		/* 评论内容 */
		.comment-item-detail {
			padding: 20px;
			padding-bottom: 0px;
			
			.comment-item-header {
				margin-bottom: 10px;
				font-size: 12px;
				
				div {
					font-size: 14px;
					text-align: right;
					margin-bottom: 10px;
				}
				
				//回复按钮
				.reply {
					color: #409EFF;
					
				}
				.reply:hover {
					text-decoration: underline;
					cursor: pointer;
				}
			}
			
			.comment-item-bottom {
				color: #666666;
				font-size: 12px;
				text-align: right;
				margin: 5px 0;
				
				span:hover {
					color: #409EFF;
					cursor: pointer;
				}
			}
		}
		
		.reply-container {
			li {
				display: flex;
				align-items: center;
				padding: 10px;
				.reply-detail {
					font-size: 12px;
					margin-left: 10px;
				}
			}
			
		}
		
		
	}
	/* 回复框样式 */
	.reply-info {
		color: #808080;
		background-color: #FAFAFA;
		padding: 10px;
		margin-bottom: 20px;
		span {
			margin: 0 10px 5px 0;
		}
	}
	
	.el-button {
		margin-bottom: 20px;
	}
	
	/**
	 * xs样式
	 */
	
	/* 用户信息样式 */
	.detail-item-user-mobile {
		padding: 10px;
		font-size: 14px;
		background-color: #F8F8F8;
		
		.item-user-mobile {
			display: flex;
			justify-content: space-between;
		}
		
		.item-user-mobile > div {
			display: flex;
			align-items: center;
			//用户信息
			.item-user-info-mobile {
				margin-left: 10px;
				p {
					font-size: 14px;
					font-weight: 600;
				}
				span {
					font-size: 12px;
					color: #888888;
					display: inline-block;
					margin-right: 10px;
					margin-top: 5px;
				}
				span:hover {
					color: #409EFF;
					cursor: pointer;
					text-decoration: underline;
				}
			}
		}
	}
	
	
</style>
